import axios from 'axios';
import { Subject } from "rxjs";
import { TOKEN } from "./SERVER_CONST";

// Add a request interceptor
const isLocal = true;

let axiosInstance = axios.create({
    baseURL: process.env.REACT_APP_BASE_URL,
});

export let isLoggedIn = true;

export function getToken() {
    return localStorage.getItem(TOKEN);
}

export function removeToken() {
    console.log('token removed');
    localStorage.removeItem('token');
}

function setToken(token) {
    console.log('token set', token);
    localStorage.setItem('token', JSON.stringify(token.token));
}

export function setLoggedIn(bool) {
    isLoggedIn = bool;
    userLogged.next(bool)
}

const userLogged = new Subject();
export const userLogged$ = userLogged.asObservable();

const messageToShow = new Subject();
export const messageToShow$ = messageToShow.asObservable();
const PLEASE_CONNECT = "Veuillez vous connecter svp"

axiosInstance.interceptors.request.use(function (config) {
    // Do something before request is sent
    //If the header does not contain the token and the url not public, redirect to login
    let accessToken = getToken();

    //if token is found add it to the header
    if (accessToken) {
        if (config.method !== 'OPTIONS') {
            config.headers.authorization = accessToken;
        }
    }else{
        setLoggedIn(false);
    }
    return config;
}, function (error) {
    // Do something with request error
    return Promise.reject(error);
});

axiosInstance.interceptors.response.use(function (response) {
    // Do something with response data
    console.log(response);
    if (response.data['message']){
        messageToShow.next(response.data['message']);
    }
    switch (response.status) {
        case 200:
            if (response.request.responseURL.indexOf('authentication') > 0) {
                setLoggedIn(true);
                let token = response.data;

                console.log('token obtained ',  token);
                token ? setToken(token) : console.log('null');
                return token
            }
            return response.data ? response.data : true;
        default :
            console.log('unknwon error');
            return response
    }
}, function (error) {
    // Do something with response error
    console.log(error.response, error.response);
    if (error.response) {
        if (error.response.data['message']){
            messageToShow.next(error.response.data['message']);
        }
        switch (error.response.status) {
            case 401 :
                console.log('bad credentials ');
                setLoggedIn(false);
                return Promise.reject(error);
            default :
                console.log('Mauvaise requete');
                return Promise.reject(error);
        }
    } else {
        console.log('Impossible to log in ');
        setLoggedIn(false);
        return Promise.reject(error);
    }
});

console.log(axiosInstance);

export default axiosInstance