import React, { Component } from 'react';
import './bill_display.css'
import { numbToLetters } from "../useful_functions/numbToLetters";
import { ListElementSelector } from "../add_customer_account/ListElementSelector";
import { BillDisplay } from "./bill_display";
import { billServerService } from "../server_classes/serverData";
import cloneDeep from 'lodash/cloneDeep';
import { StatesByDate } from "../states_by_date/states_by_date";


export class BillViewer extends Component {
    DELETE_BILL = 'Supprimer facture';

    constructor(props) {
        super(props);
        this.state = {
            billToShow: {},
            ticketsToShow: [],
            accountToShow: {},
            allBills: [],
            dateToShow: '',
            isDeleteBillButtonDisabled: false,
            billTotalMoneyNumbers: {
                totalToPay: 0,
                fees: 0,
                discount: 0,
                TVA: 0,
                netToPay: 0,
                netToPayInLetters: numbToLetters(0),
            }
        }
    }

    componentDidMount() {
        this.getAllBills(this);
    }

    getValueToBill(accountToShow) {
        return accountToShow['with FS'] ? 'total' : 'priceTTC'
    }

    getAllBills(self) {
        setTimeout(() =>billServerService.getAllBills()
            .then(function (response) {
                self.setState({
                    allBills: response
                });
            }), 100);
    }

    deleteBill(billToShow, self) {
        self.setState({
            isDeleteBillButtonDisabled: true,
        });
        billServerService.cancelBill(billToShow._id).then(function (response) {
            console.log(response);
            let newAllBills = cloneDeep(self.state.allBills);
            newAllBills.splice(self.state.allBills.findIndex(x => x._id === billToShow._id), 1);
            self.setState({
                billToShow: {},
                ticketsToShow: [],
                accountToShow: {},
                isDeleteBillButtonDisabled: false,
                allBills: newAllBills
            });
            response['ticketsUpdated'].map(ticket => self.props.updateTicket(ticket));
        }).catch(function (error) {
            self.setState({
                isDeleteBillButtonDisabled: false,
            });
            console.log("petite erreur l'ami, ressaie", error);
        });
    }


    handleClickOnBillToShow(billToShow) {
        console.log(billToShow);
        let ticketsToShow = this.props.allTickets.filter(
            ticket => billToShow.tickets.reduce(
                (finalBool, billToShowSubTickets) => finalBool || billToShowSubTickets.includes(ticket._id), false
            )
        ).sort((a, b) => StatesByDate.sortAlphabatically(a.date + a.ticketNumber.substring(4), b.date + b.ticketNumber.substring(4)));
        let accountToShow = this.props.allAccount.filter(account => account._id === billToShow.customerAccount)[0];

        let totalToPay = ticketsToShow.reduce((sum, current) => sum + parseInt(current[this.getValueToBill(accountToShow)], 10), 0);
        let fees = 0;
        let discount = 0;
        let TVA = 0;
        let netToPay = totalToPay + fees + discount + TVA;
        let billTotalMoneyNumbers = {
            totalToPay: totalToPay,
            fees: 0,
            discount: 0,
            TVA: 0,
            netToPay: totalToPay + fees + discount + TVA,
            netToPayInLetters: numbToLetters(netToPay),
        };
        this.setState({
            billToShow: billToShow,
            ticketsToShow: ticketsToShow,
            accountToShow: accountToShow,
            billTotalMoneyNumbers: billTotalMoneyNumbers,
            dateToShow: billToShow.creationDate,
        });
    }

    getTicketsFromBill(bill) {
        return this.props.allTickets.filter(
            ticket => bill.tickets.reduce(
                (finalBool, billToShowSubTickets) => finalBool || billToShowSubTickets.includes(ticket._id), false
            )
        );
    }

    getAccountFromBill(bill) {
        return this.props.allAccount.find(account => account._id === bill.customerAccount);
    }

    addDataToBill(bill) {
        if (!bill) return;
        let ticketsFromBill = this.getTicketsFromBill(bill);
        let passengerTickets = ticketsFromBill.reduce((finalValue, currentTicket) => finalValue + currentTicket.name + ' ', '');
        let accountFromBill = this.getAccountFromBill(bill);
        return {
            accountName: accountFromBill.name,
            billNumber: bill.billNumber,
            passengerName: passengerTickets,
            passengerNumber: ticketsFromBill.length,
            passengerValueToShow: ticketsFromBill.length.toString() + ' voyageurs',
            bill: bill,
            canceled: bill.canceled ? 'Annule' : ''
        }
    }

    render() {
        console.log('allBills length', this.state.allBills.length);
        let dataToShow = this.state.allBills.map(x => this.addDataToBill(x));

        return <div className=" margin-left-right-auto flex-container flex-direction-column">

            Liste factures :
            <ListElementSelector
                onValid={(billToShow) => this.handleClickOnBillToShow(billToShow.bill)}
                data={dataToShow}
                dataKeysToShow={x => ["accountName", "billNumber"].concat(x.passengerNumber > 4 ? ['passengerValueToShow'] : ["passengerName"]).concat(["canceled"])}
                filterPossibilities={["accountName", "billNumber", "passengerName"]}
                sortBy={['accountName', 'billNumber']}
                multiChoice={false}/>

            {/*<ListElement dataToRender={dataToShow}*/}
            {/*onElementClick={(billToShow) => this.handleClickOnBillToShow(billToShow.bill)}*/}
            {/*dataKeysToShow={["accountName", "billNumber", "passengerName"]}*/}
            {/*dataAlreadySelected={[]}*/}
            {/*/>*/}

            <BillDisplay printable={true}
                         accountSelected={this.state.accountToShow}
                         ticketsSelected={this.state.ticketsToShow}
                         dateToShow={this.state.dateToShow}
                         billNumber={this.state.billToShow.billNumber}
                         commandNumber={this.state.billToShow.commandNumber}
                         remarks={this.state.billToShow.remarks}
                         valueToBill={this.state.billToShow.valueBilled ? this.state.billToShow.valueBilled : this.state.accountToShow['with FS'] ? 'total' : 'priceTTC'}
                         showDetailsData={true}
            />
            <button className={this.state.ticketsToShow.length > 0 ? '' : 'displayNone'}
                    onClick={() => this.deleteBill(this.state.billToShow, this)}
                    disabled={this.state.isDeleteBillButtonDisabled}>
                {this.DELETE_BILL}
            </button>
        </div>
    }
}