import React, { Component } from 'react';
import { SuperDataTable } from "./superDataTable";


export class StatesComponent extends Component {
    constructor(props) {
        super(props);
        this.state = {
            dataToShow: []
        }
    }

    showButtonToChangeDataToShow() {
        let arrayOfData = [
            {name: 'Tickets', data: this.props.allTickets},
            {name: 'Comptes', data: this.props.allAccount},
            {name: 'Avoirs', data: this.props.allTickets},
        ]
        return <div style={{margin: '10px'}}>
            {arrayOfData.map(x=> <button key={x.name} onClick={()=>this.setState({dataToShow:x.data})}>{x.name}</button>)}
            </div>
    }

    render() {

        return <div>
            {this.showButtonToChangeDataToShow()}
            <SuperDataTable
                // allAccount={this.state.allAccount}
                // dataToShow={this.state.allTickets}
                dataToShow={this.state.dataToShow}
            />
        </div>
    }
}