import React, { Component } from 'react';
import './App.css';
import cloneDeep from 'lodash/cloneDeep';
import { AirReaderModal } from './air_reader_modal/air_reader_modal';
import { billServerService } from './server_classes/serverData';
import { CustomersComponent } from './add_customer_account/customersComponent';
import { StatesByDate } from './states_by_date/states_by_date';
import { CreditNoteView } from './credit_note/CreditNoteView';

import { Route } from 'react-router-dom';
import { TicketView } from './ticket-validation/TicketView';
import { getToken, removeToken, setLoggedIn, userLogged$ } from './server_classes/axiosInstance';
import { ImportAIRComponent } from './import_tickets/ImportAIRComponent';
import { TestComponent } from './useful_functions/TestComponent';
import { BOComponent } from './useful_functions/BOComponent';
import { StatesComponent } from './states_by_date/statesComponent';
import AirReaderAppBar from './components/app_bar/AirReaderAppBar';
import ConnexionDialog from './Dialogs/ConnexionDialog';
import MessageSnackBar from './Dialogs/MessageSnackBar';
import { ServerService } from './server_classes/ServerService';
import { ACCOUNTS_PATH } from './server_classes/SERVER_CONST';
import { ControlComponent } from './control_component/ControlComponent';
import AirReaderMenuList from './components/app_bar/AirReaderMenuList';
import { DashBoardView } from './components/dashboard/dashboardView';
import { ImportBankData } from './components/bank/ImportBankData';


class App extends Component {
    constructor() {
        super();
        this.state = {
            allAccount: [],
            allTickets: [],
            inputValue: 'lol',
            modalVisibility: false,
            modalComponent: '',
            loggedIn: true,
            ticketStatus: '',
            accountStatus: '',
            connexionDialogOpen: false,
        };
        userLogged$.subscribe(
            loggedIn => {
                this.setLoggedIn(loggedIn);
                console.log('that\'s it');
            });
    }

    componentDidMount() {
        this.getAllAccounts(this);
        this.getAllTickets(this);
        this.setDecodedToken();
    }

    setDecodedToken() {
        let token = getToken();
        if (!getToken() || getToken().length < 1) return;
        this.setState({
            decodedToken: JSON.parse(window.atob(token.split('.')[1])),
        });
    }

    setLoggedIn(isLoggedInBool) {
        this.setState({
            loggedIn: isLoggedInBool,
        });
        this.setConnexionDialogOpen(!isLoggedInBool);
        if (isLoggedInBool) {
            this.componentDidMount();
        }
        console.log('loggedIn : ', this.state.loggedIn);
    }

    logOutFromApp() {
        removeToken();
        setLoggedIn(false);
        this.setLoggedIn(false);
        this.setState({
            allAccount: [],
            allTickets: [],
        });
        this.resetStatus();
    }

    getAllAccounts(self) {
        this.setState({
            accountStatus: 'Comptes en cours de chargement',
        });
        // billServerService.getAllAccounts()
        ServerService.getFromServer(ACCOUNTS_PATH)
                     .then(function (response) {
                         console.log(response.length, ' comptes disponibles');
                         if (!response) {

                         }
                         self.setState({
                             allAccount: response,
                             accountStatus: response.length + ' comptes chargs',
                         });
                         // return response.json();
                     })
                     .catch(error => {
                         self.setState({
                             accountStatus: 'Erreur chargement comptes',
                         });
                     });
    }

    getAllTickets(self) {
        this.setState({
            ticketStatus: 'Tickets en cours de chargement',
        });
        billServerService.getAllTickets()
                         .then(function (response) {
                             let jsonResponse = response;
                             console.log(response.length, ' tickets crs');

                             if (!response) return;
                             self.setState({
                                 allTickets: jsonResponse,
                                 ticketList: jsonResponse.filter(x => x['checked'] !== true),
                                 ticketStatus: response.length + ' tickets chargs',
                             });
                         }).catch(error => {
                self.setState({
                    ticketStatus: 'Erreur chargement tickets',
                });
            },
        );
    }

    updateTicket(ticket) {
        let newTicketList = cloneDeep(this.state.allTickets);
        let ticketIndex = newTicketList.findIndex(x => x['_id'] === ticket['_id']);
        newTicketList[ticketIndex] = ticket;
        this.setState({
            allTickets: newTicketList,
        });
    }

    addTicket(ticketToAdd) {
        this.setState({
            allTickets: this.state.allTickets.concat([ticketToAdd]),
        });
    }


    addNewAccount(accountToAdd) {
        let newAccountList = cloneDeep(this.state.allAccount);
        newAccountList.push(accountToAdd);
        this.setState({
            allAccount: newAccountList,
        });
        console.log('ticket updated', newAccountList, accountToAdd);
    }

    updateAccount(accountModified) {
        let newAccountList = cloneDeep(this.state.allAccount);
        let accountIndex = newAccountList.findIndex(x => x['_id'] === accountModified['_id']);
        newAccountList[accountIndex] = accountModified;
        this.setState({
            allAccount: newAccountList,
        });
    }


    deleteAccount(accountDeleted) {
        let newAccountList = cloneDeep(this.state.allAccount);
        let accountIndex = newAccountList.findIndex(x => x['_id'] === accountDeleted['_id']);
        newAccountList.splice(accountIndex, 1);
        this.setState({
            allAccount: newAccountList,
        });
    }

    setModalVisibility(visibility, compToShow) {
        if (this.state.modalVisibility !== visibility || visibility) {
            this.setState({
                modalVisibility: visibility,
            });
        }
        if (compToShow) {

            this.setState({
                modalComponent: compToShow,
            });
        }
    }


    resetStatus() {
        setTimeout(() => {
            this.setState({
                ticketStatus: this.state.allTickets.length + ' tickets chargs',
                accountStatus: this.state.allAccount.length + ' comptes chargs',
            });
        }, 100);
    }

    getStatusTextDiv() {
        return <div className="flex-1-1">{
            [this.state.ticketStatus, this.state.accountStatus].map((x, step) =>
                <div key={step}>{x}</div>)
        }
        </div>;
    }

    getLogInDiv() {
        return this.state.loggedIn ?
            <div>
                <div>{this.state.decodedToken ? this.state.decodedToken.name : ''}</div>
                <div onClick={() => this.logOutFromApp()}>Se dconnecter</div>
            </div> :
            <div>
                <div><strong>Non connect</strong></div>
                <div onClick={() => this.setLoggedIn(false)}>Se connecter</div>
            </div>;
    }

    setConnexionDialogOpen(bool) {
        this.setState({connexionDialogOpen: bool});
    }

    render() {
        console.log(process.env.REACT_APP_BASE_URL);
        return (
            <div className="App main-container">
                <AirReaderAppBar className="sticky-top-0" logInDiv={this.getLogInDiv()}
                                 statusDiv={this.getStatusTextDiv()}/>

                <div className="flex-container " style={{marginTop: '64px'}}>
                    <div>
                        <AirReaderMenuList/>
                    </div>
                    <div style={{overflowX: 'auto', marginTop: '20px'}} className="flex-1-1">

                        <Route path="/tickets" render={() =>
                            <TicketView
                                allAccount={this.state.allAccount}
                                allTickets={this.state.allTickets}
                                updateTicket={(x) => this.updateTicket(x)}
                                addTicket={(ticketToAdd) => this.addTicket(ticketToAdd)}/>
                        }/>
                        <Route path="/accounts" render={() =>
                            <CustomersComponent allAccount={this.state.allAccount}
                                                addNewAccount={(newAccount) => this.addNewAccount(newAccount)}
                                                updateAccount={(accountUpdated) => this.updateAccount(accountUpdated)}
                                                deleteAccount={(accountDeleted) => this.deleteAccount(accountDeleted)}/>
                        }/>
                        <Route path="/credit_notes" render={() =>
                            <CreditNoteView
                                allAccount={this.state.allAccount}
                                allTickets={this.state.allTickets}
                                updateTicket={(x) => this.updateTicket(x)}
                            />
                        }/>
                        <Route path="/creditNote" render={() =>
                            <CustomersComponent allAccount={this.state.allAccount}
                                                addNewAccount={(newAccount) => this.addNewAccount(newAccount)}
                                                updateAccount={(accountUpdated) => this.updateAccount(accountUpdated)}
                                                deleteAccount={(accountDeleted) => this.deleteAccount(accountDeleted)}/>
                        }/>

                        <Route path="/states" render={() =>
                            <StatesByDate
                                allAccount={this.state.allAccount}
                                allTickets={this.state.allTickets}
                            />}
                        />
                        <Route path="/import" render={() =>
                            <ImportAIRComponent
                                allAccount={this.state.allAccount}
                                allTickets={this.state.allTickets}
                            />}
                        />
                        <Route path="/test" render={() =>
                            <TestComponent
                                allAccount={this.state.allAccount}
                                allTickets={this.state.allTickets}
                            />}
                        />
                        <Route path="/superDataTable" render={() =>
                            <StatesComponent
                                allAccount={this.state.allAccount}
                                allTickets={this.state.allTickets}/>
                        }
                        />
                        <Route path="/boComponent" render={() =>
                            <BOComponent
                                // allAccount={this.state.allAccount}
                                // dataToShow={this.state.allTickets}
                                dataToShow={recentCreditNotes}
                            />}
                        />
                        <Route path="/controlComponent" render={() =>
                            <ControlComponent
                                // allAccount={this.state.allAccount}
                                // dataToShow={this.state.allTickets}
                                allAccount={this.state.allAccount}
                            />}
                        />
                        <Route path="/dashboard"
                               render={() =>
                            <DashBoardView/>
                        }
                        />
                        <Route path="/bank"
                               render={() =>
                            <ImportBankData/>
                        }
                        />


                    </div>
                </div>


                <AirReaderModal visible={this.state.modalVisibility}
                                onOutsideClick={() => this.setModalVisibility(false, '')}>
                    {this.state.modalComponent}
                </AirReaderModal>

                <ConnexionDialog connexionDialogOpen={this.state.connexionDialogOpen}
                                 handleClose={() => this.setConnexionDialogOpen(false)}
                />

                <MessageSnackBar/>

            </div>
        );
    }
}

export default App;


