import React from 'react';

export function LabelAndInputElement(props) {
    //props : inputValue, keyToModify, onChange, inputType, labelValue
    return <div className={props.className ? props.className : 'flex-container'}>
        <label className="flex-1-1 margin-3-2">{props.labelValue}</label>
        {
            props.inputType === 'checkbox' ?
                <input
                    name={props.keyToModify}
                    checked={props.inputValue} type={props.inputType}
                    onChange={(event) => props.onChange(event)}
                    className="flex-1-1"/> :
                <input
                    name={props.keyToModify}
                    value={props.inputValue} type={props.inputType}
                    onChange={(event) => props.onChange(event)}
                    className="flex-1-1"/>
        }
    </div>
}