import React, { Component } from 'react';
import { ListElement} from "../add_customer_account/showCustomerAccounts";
import { CreditNoteServerService } from "../server_classes/creditNoteServerRequest";
import { numbToLetters } from "../useful_functions/numbToLetters";
import { AccountDetails } from "../add_customer_account/accountDetails";
import { CreditNoteBill } from "./CreditNoteBill";
import { ListElementSelector } from "../add_customer_account/ListElementSelector";


export class CreditNoteBillMaker extends Component {
    constructor(props) {
        super(props);
        this.state = {
            billCreationStage: 0,
            accountSelected: '',
            creditNotesSelected: [],
            creditNoteNumber: '',
            billTotalMoneyNumbers: '',
            isValidateBillButtonDisabled: '',
            validateButtonMessage: '',
            commandNumber: '',
            remarks: '',
            dateToShow: new Date().getTime(),
            paymentType: '',
        }
    }

    onAccountSelected(accountSelected) {
        console.log("accountSelected", accountSelected);
        this.setState({
            accountSelected: accountSelected,
            creditNotesSelected: [],
            paymentType: accountSelected['paiement type'],
        });
        this.setBillCreationStage(1)
    }

    setBillCreationStage(value) {
        this.setState({
            billCreationStage: value
        })
    }

    unselectAccount() {
        this.setBillCreationStage(0)
        this.setState({
            accountSelected: ''
        })
    }

    getValueToBill() {
        return this.state.accountSelected['with FS'] ? 'total' : 'priceTTC'
    }

    onCreditNotesSelected(creditNotesSelected) {
        console.log("creditNotesSelected", creditNotesSelected);

        let totalToPay = creditNotesSelected.reduce((sum, current) => sum + parseInt(current[this.getValueToBill()], 10), 0);
        let fees = 0;
        let discount = 0;
        let TVA = 0;
        let netToPay = totalToPay + fees + discount + TVA;
        let billTotalMoneyNumbers = {
            totalToPay: totalToPay,
            fees: 0,
            discount: 0,
            TVA: 0,
            netToPay: totalToPay + fees + discount + TVA,
            netToPayInLetters: numbToLetters(netToPay),
        };
        CreditNoteServerService.getNextCreditNoteBillNumber().then(creditNoteNumber => this.setState({
            creditNoteNumber: creditNoteNumber
        }));
        this.setState({
            creditNotesSelected: creditNotesSelected,
            billTotalMoneyNumbers: billTotalMoneyNumbers,
            isValidateBillButtonDisabled: false,
            validateButtonMessage: this.VALIDATE_BILL
        });
        this.setBillCreationStage(2)
    }

    filterFunction(x, self) {
        return x['customerId'] === self.state.accountSelected['_id']
            && !x['canceled']
            && !x['billed']
    }

    handleCreditNoteBillMakerInputChange(event) {
        this.setState({
            [event.target.name]: event.target.value,
        });
    }

    generateCreditNoteBillAndSend(self) {
        let creditNoteBill = {
            billNumber: this.state.creditNoteNumber,
            credit_notes: this.state.creditNotesSelected.map(creditNote => [creditNote['_id'], creditNote['netToRefund']]),
            customerId: this.state.accountSelected._id,
            customerAccount: this.state.accountSelected.name,
            totalBilled: this.state.creditNotesSelected.reduce((result, creditNote) => {
                result += creditNote['netToRefund'];
                return result
            }, 0)
        };
        console.log('creditNoteBill', creditNoteBill);
        CreditNoteServerService.insertCreditNoteBill(creditNoteBill).then(creditNoteBillReturned => {
            if (!creditNoteBillReturned) return;
            console.log('creditNoteBillReturned', creditNoteBillReturned);
            self.props.addCreditNoteBill(creditNoteBillReturned['credit_note_bill'], creditNoteBillReturned['credit_notes_updated']);
            self.unselectAccount();
        })
    }

    render() {
        let dataToShow = this.props.allCreditNotes.filter(x => this.filterFunction(x, this));
        console.log(dataToShow);

        return <div className="flex-container flex-direction-column ">
            <div className="margin-left-right-auto">
                <div onClick={() => this.unselectAccount()}>Choisir compte</div>
                <ListElementSelector onClick={x => console.log(x)}
                                     onValid={accountSelected => {
                                         this.onAccountSelected(accountSelected);
                                     }}
                                     data={this.props.allAccount}
                                     sortBy='name'
                                     dataKeysToShow={['name', 'customer reference']}
                                     filterPossibilities={['name', 'customer reference']}
                                     multiChoice={false}
                                     className={this.state.billCreationStage === 0 ? '' : 'displayNone'}
                />
                <AccountDetails accountToShow={this.state.accountSelected}
                                className={this.state.billCreationStage > 0 ? '' : 'displayNone'}/>
            </div>

            <div
                className={'margin-left-right-auto margin-bottom-20px ' + (this.state.accountSelected ? '' : 'displayNone')}>
                <div onClick={() => this.setBillCreationStage(1)}>Selectionner avoirs</div>
                <ListElementSelector onClick={x => console.log(x)}
                                     onValid={x => {
                                         this.onCreditNotesSelected(x)
                                     }}
                                     data={dataToShow}
                                     dataKeysToShow={['ticketNumber', 'passengerName', 'initialTicketAmount', 'netToRefund']}
                                     filterPossibilities={['ticketNumber', 'passengerName']}
                                     multiChoice={true}
                                     className={this.state.billCreationStage === 1 ? '' : 'displayNone'}
                />
                <ListElement dataToRender={this.state.creditNotesSelected}
                             onElementClick={(element) => console.log(element)}
                             dataKeysToShow={['ticketNumber', 'passengerName', 'initialTicketAmount', 'netToRefund']}
                             className={this.state.billCreationStage > 1 ? '' : 'displayNone'}
                />
            </div>

            <div className="flex-container padding-20px margin-20-px">

                <div>

                    <div
                        className={'margin-left-right-auto position-sticky text-left '
                        + (this.state.creditNotesSelected.length > 0 ? '' : 'displayNone')}>
                        <div>
                            <label>Facture n : </label>
                            <input name='creditNoteNumber' value={this.state.creditNoteNumber}
                                   onChange={(e) => this.handleCreditNoteBillMakerInputChange(e)}/>
                        </div>
                        <div>
                            <label>Bon de commande : </label>
                            <input name='commandNumber' onChange={(e) => this.handleCreditNoteBillMakerInputChange(e)}/>
                        </div>
                        <div>
                            <label>Mode de paiement : </label>
                            <input name='paymentType' value={this.state.paymentType}
                                   onChange={(e) => this.handleCreditNoteBillMakerInputChange(e)}/>
                        </div>
                        <div>
                            <label className="align-top">Remarques : </label>
                            <textarea id="remark-field-bill-maker" name='remarks'
                                      onChange={(e) => this.handleCreditNoteBillMakerInputChange(e)}/>
                        </div>

                        <button className={this.state.creditNotesSelected.length > 0 ? '' : 'displayNone '}
                                onClick={() => this.generateCreditNoteBillAndSend(this)}
                                disabled={this.state.isValidateBillButtonDisabled}>
                            {this.state.validateButtonMessage} Valider
                        </button>
                    </div>


                </div>
                <CreditNoteBill printable={false}
                    accountSelected={this.state.accountSelected}
                                creditNotes={this.state.creditNotesSelected}
                                dateToShow={this.state.dateToShow}
                                billNumber={this.state.creditNoteNumber}
                                commandNumber={this.state.commandNumber}
                                remarks={this.state.remarks}
                                valueToBill={this.state.valueToBill}
                                showDetailsData={true}
                                paymentType={this.state.paymentType}
                                className="flex-1-1">

                </CreditNoteBill>
            </div>
        </div>
    }
}