import axiosInstance from "./axiosInstance";

const CREDIT_NOTE = 'credit_note/';
const CREDIT_NOTE_BILL = 'credit_note/bill/';


export class CreditNoteServerService {

    static cancelCreditNote(credit_note) {
        return axiosInstance
            .post(CREDIT_NOTE + 'cancel', JSON.stringify(credit_note['_id']))
    }

    static insertCreditNote(credit_noteToInsert) {
        return axiosInstance
            .post(CREDIT_NOTE + 'insert', JSON.stringify(credit_noteToInsert))
    }

    static insertCreditNoteBill(creditNoteBillToInsert) {
        return axiosInstance
            .post(CREDIT_NOTE_BILL + 'insert', JSON.stringify(creditNoteBillToInsert))
    }


    static cancelCreditNoteBillRequest(creditNoteBillToCancel) {
        return axiosInstance
            .post(CREDIT_NOTE_BILL + 'cancel', JSON.stringify(creditNoteBillToCancel._id))
    }

    static getAllCreditNoteBills() {
        return axiosInstance.get(CREDIT_NOTE_BILL)

        // return axios.get(CREDIT_NOTE_BILL).then(response => {
        //     if (response.status === 200) {
        //         console.log(response);
        //         return response.json();
        //     }
        // })
    }

    static getAllCreditNotes() {
        return axiosInstance.get(CREDIT_NOTE)
    }

    static getNextCreditNoteBillNumber() {
        return axiosInstance.get(CREDIT_NOTE + 'getNextCreditNoteBillNumber')
            .then(creditNoteNumber =>
                "AFV/18/" + "000000".substring(creditNoteNumber.toString().length) + creditNoteNumber.toString() + "AB"
            )
    }

}
