import React  from 'react';

export function AccountDetails(props) {
    return <div className={props.className + ' text-left' }>
        <div>{props.accountToShow['name']}</div>
        <div>{props.accountToShow['customer reference']}</div>
        <div>{props.accountToShow['adress']}</div>
        <div>{props.accountToShow['city'] + ' - ' + props.accountToShow['country']}</div>
        <div>{props.accountToShow['entire name']}</div>
        <div>{"Avec FS : " + props.accountToShow['with FS']}</div>
        <div>{"Type de paiement : " + props.accountToShow['paiement type']}</div>
    </div>
}