import React from 'react';
import TextField from '@material-ui/core/es/TextField/TextField';
import { ServerService } from '../../server_classes/ServerService';
import { TICKETS_PATH } from '../../server_classes/SERVER_CONST';
import {
    Area,
    AreaChart,
    Bar,
    BarChart,
    CartesianGrid,
    Legend,
    Line,
    LineChart,
    Pie,
    PieChart,
    ResponsiveContainer,
    Tooltip,
    XAxis,
    YAxis,
} from 'recharts';
import Card from '@material-ui/core/es/Card/Card';
import CardContent from '@material-ui/core/es/CardContent/CardContent';
import InputAdornment from '@material-ui/core/es/InputAdornment/InputAdornment';
import ChordDiagram from 'react-chord-diagram/es/ChordDiagram';
import { ColorConverter } from '../../useful_functions/ColorConverter';

export class DashBoardView extends React.Component {
    CANCELED_PROPERTY = 'canceled';
    state = {
        filter: {
            beginDate: '',
            endDate: '',
        },
        tickets: [],
        ticketsHandled: [],
        generalDatas: {},
        chordDiagramMatrix: [],
        chordListLegend: [],
        chordListColors:[],
        bankDatas: {iata: '', ecobank: '', scb: '', scbDecouvert: '', cashier: '', BOs: '', result: ''},

    };

    handleFilterChange(e) {
        let newFilterDate = Object.assign({}, this.state.filter,
            {[e.target.name]: e.target.value});
        this.setState({
            filter: newFilterDate,
        });
    }

    handleBankDataChange(e) {
        let newBankDatas = Object.assign({}, this.state.bankDatas,
            {[e.target.name]: e.target.value});
        this.setState({
            bankDatas: newBankDatas,
        });
    }


    makeNextDayDate(dateToModify) {
        return dateToModify.substr(0, dateToModify.length - 1) + (parseInt(dateToModify.substr(dateToModify.length - 1)) + 1);
    }

    formatDateForTicketRequest(dateToFormat) {
        return dateToFormat.substr(2).split('-').join('');
    }

    getRecentBills() {
        let dateBillFilter = [];
        if (this.state.filter.beginDate) {
            dateBillFilter.push({date: {$gte: this.formatDateForTicketRequest(this.state.filter.beginDate)}});
        }
        if (this.state.filter.endDate) {
            dateBillFilter.push({date: {$lte: this.formatDateForTicketRequest(this.state.filter.endDate)}});
        }
        switch (dateBillFilter.length) {
            case 0:
                return null;
            case 1:
                dateBillFilter = dateBillFilter[0];
                break;
            default:
                dateBillFilter = {$and: dateBillFilter};
        }
        console.log(dateBillFilter);
        ServerService.getFromServer(TICKETS_PATH, dateBillFilter, {sort: {date: 1}, limit: 0})
                     .then(results => {
                         console.log(results.map(x => x.canceled));
                         this.setState({tickets: results});
                         this.handleTicketData(results);
                     });
    }

    sortObjectsByKey(objectList, keyValue) {
        return objectList.reduce((finalArray, currentObject) => {
            if (!finalArray.hasOwnProperty(currentObject[keyValue])) {
                finalArray[currentObject[keyValue]] = {data: [currentObject]};
                return finalArray;
            }
            finalArray[currentObject[keyValue]].data.push(currentObject);
            return finalArray;
        }, {});
    }

    getGeneralDatasFromTickets(tickets) {
        let generalDatas = tickets.reduce((acc, cur) => {
            if (!cur.canceled) {
                acc.totalCommission += parseInt(cur.commissionValue);
                acc.totalAmountSold += parseInt(cur.total);
                acc.totalPriceTTC += parseInt(cur.priceTTC);
                acc.totalPriceHT += parseInt(cur.priceHT);
                acc.totalTaxes += parseInt(cur.priceTTC) - parseInt(cur.priceHT);
            } //ICI A MODIFIER SI CHANGEMENT CONSIDERATION FS ....
            acc.totalFees += cur.fees ? parseInt(cur.fees) : 0;
            return acc;
        }, {
            totalTicketsNumber: tickets.length,
            totalCanceledTicketsNumber: tickets.filter(x => x.hasOwnProperty(this.CANCELED_PROPERTY)).length,
            totalPriceTTC: 0,
            totalPriceHT: 0,
            totalTaxes: 0,
            totalAmountSold: 0,
            totalFees: 0,
            totalCommission: 0,
        });


        this.setState({generalDatas: generalDatas});
    }

    handleTicketData(tickets) {
        this.getGeneralDatasFromTickets(tickets);
        this.setChordsData(tickets);
        let ticketsGroupedByDate = tickets.reduce((finalArray, currentTicket) => {
            if (!finalArray.hasOwnProperty(currentTicket.date)) {
                finalArray[currentTicket.date] = {tickets: [currentTicket]};
                return finalArray;
            }
            finalArray[currentTicket.date].tickets.push(currentTicket);
            return finalArray;
        }, {});
        let finalTickets = [];
        for (let key of Object.keys(ticketsGroupedByDate)) {
            let newSynthetisedObject = {date: key, tickets: ticketsGroupedByDate[key].tickets};
            newSynthetisedObject = Object.assign(newSynthetisedObject,
                newSynthetisedObject.tickets.reduce((finalValue, currentTicket) => {
                        finalValue.totalValue += currentTicket.total;
                        finalValue.fees += currentTicket.fees ? parseInt(currentTicket.fees) : 0;
                        finalValue.commissionValue += parseInt(currentTicket.commissionValue);
                        finalValue.priceTTC += parseInt(currentTicket.priceTTC);
                        return finalValue;
                    },
                    {totalValue: 0, fees: 0, commissionValue: 0, priceTTC: 0},
                ),
            );

            finalTickets.push(newSynthetisedObject);

        }
        console.log(finalTickets);
        this.setState({ticketsHandled: finalTickets});
    }

    addKeyToTableauObject(tableauObject, key, tableauChords, keyrank) {
        if (tableauObject.hasOwnProperty(key)) {
            return {tableauObject, tableauChords, keyrank};
        }
        tableauObject[key] = keyrank;
        tableauChords.push([...Array(keyrank)].map((u, i) => 0));
        for (key of Object.keys(tableauObject)) {
            tableauChords[tableauObject[key]].push(0);
        }
        keyrank += 1;
        return {tableauObject, tableauChords, keyrank};
    }


    generateChordsData(tableauWithAllTravel) {
        let tableauChords = [];
        let tableauObject = {};
        let keyrank = 0;
        let departureCity = '', arrivalCity = '';
        for (let i = 0; i < tableauWithAllTravel.length / 2; i++) {
            ([departureCity = '', arrivalCity = ''] = tableauWithAllTravel.slice(2 * i, 2 * i + 2));
            ({
                keyrank =0,
                tableauObject={},
                tableauChords=[],
            } = this.addKeyToTableauObject(tableauObject, departureCity, tableauChords, keyrank));
            ({
                keyrank =0,
                tableauObject={},
                tableauChords=[],
            } = this.addKeyToTableauObject(tableauObject, arrivalCity, tableauChords, keyrank));
            tableauChords[tableauObject[arrivalCity]][tableauObject[departureCity]] += 1;
        }
        console.log(Object.keys(tableauObject));
        return {tableauObject, tableauChords};
    }

    retrieveAllCitiesStop(tickets) {
        let citiesStopPerTicket  = tickets.map(x => x.tripsInDetail.reduce((acc, cur, step) => {

            if (cur.tripType === 'X') {
                if (step ===0){
                    acc.push(cur.departureAirportCode);
                }
                if (step < x.tripsInDetail.length - 1 && x.tripsInDetail[step + 1].tripType === 'O') {
                    acc.push(cur.arrivalAirportCode);
                }
            } else {
                acc.push(cur.departureAirportCode);
                if (step < x.tripsInDetail.length - 1 && x.tripsInDetail[step + 1].tripType === 'O') {
                    acc.push(cur.arrivalAirportCode);
                }
            }
            if (step === x.tripsInDetail.length - 1) {
                acc.push(cur.arrivalAirportCode);
            }
            return acc;
        }, []));
        return [].concat.apply([], citiesStopPerTicket)
    }

    setChordsData(tickets){
        console.log('ALLStops',tickets,this.retrieveAllCitiesStop(tickets));
        let allCitiesStop = this.retrieveAllCitiesStop(tickets);
        let {tableauObject={}, tableauChords=[]}=this.generateChordsData(allCitiesStop);
        let colorNumber = Object.keys(tableauObject).length;
        let colorSpacing = parseInt('FFFFFF',16)/(colorNumber+3);
        let colorSpacing2 = 1/(colorNumber+3);
        let colorArray = [...new Array(colorNumber)].map((x,i)=> {
            let a = i*colorSpacing2;
            console.log(i, i*colorSpacing, a, ColorConverter.hslToRgb(a, 100, 50, true));
            return ColorConverter.hslToRgb(a, 1, 0.5, true);
            // return hsl(a,'100','50%');
        });
        // let colorArray = [...new Array(colorNumber)].map((x,i)=> "#" + ("00000000" +(i*colorSpacing).toString(16)).substr(-6));
        console.log(colorSpacing,colorNumber, colorArray);
        this.setState({
            chordDiagramMatrix: tableauChords,
            chordListLegend: Object.keys(tableauObject),
            chordListColors: colorArray,
        })
    }


    render() {
        let dataForPieChart1 = [
            {name: 'priceHT', 'value': this.state.generalDatas.totalPriceHT},
            {name: 'taxes', value: this.state.generalDatas.totalTaxes},
            {name: 'totalFees', value: this.state.generalDatas.totalFees}];
        console.log('data', dataForPieChart1);
        console.log('dataChords', this.state.chordListLegend, this.state.chordDiagramMatrix);


        return <div style={{padding: '5px'}}>
            <DateFilterManager
                style={{marginBottom: '10px'}}
                beginDate={this.state.filter.beginDate}
                endDate={this.state.filter.endDate}
                handleDateChange={x => this.handleFilterChange(x)}>
                <button onClick={() => this.getRecentBills()}>Recupre tes billets ami</button>
            </DateFilterManager>

            {
                Object.keys(this.state.generalDatas).map(x => <div>{x} : {this.state.generalDatas[x]}</div>)
            }

            <ChordDiagram
                style={{padding: '20px'}}
                matrix={this.state.chordDiagramMatrix}
                componentId={1}
                groupLabels={this.state.chordListLegend}
                groupColors={this.state.chordListColors}
            />
            <AirReaderPieChart data={{lil: dataForPieChart1}}/>

            <StackedBarChart data={this.state.ticketsHandled}/>
            <StackedAreaChart data={this.state.ticketsHandled}/>
            <Card>
                <CardContent>
                    <ResponsiveContainer height={320}>
                        <LineChart data={this.state.ticketsHandled}>
                            <XAxis dataKey="date"/>
                            <YAxis yAxisId='0'/>
                            <YAxis yAxisId='paid' orientation='right'/>
                            <YAxis yAxisId='fees'/>
                            <CartesianGrid vertical={false} strokeDasharray="3 3"/>
                            <Tooltip/>
                            <Legend/>
                            <Line type="monotone" name='nb billets vendus' dataKey="[tickets.length]" yAxisId='0'
                                  stroke="#82ca9d"/>
                            <Line type="monotone" name='total ventes' dataKey="totalValue" stroke="#8884d8"
                                  yAxisId='paid'
                                  activeDot={{r: 8}}/>
                            <Line type="monotone" name='FS' dataKey="fees" stroke="#f284d8" yAxisId='fees'
                                  activeDot={{r: 8}}/>
                            <Line type="monotone" name='commission' dataKey="commissionValue" stroke="#3384d8"
                                  yAxisId='fees'
                                  activeDot={{r: 8}}/>
                        </LineChart>
                    </ResponsiveContainer>
                </CardContent>
            </Card>

            <BankDatasCard className='flex-1-1' bankDatas={this.state.bankDatas}
                           handleChange={e => this.handleBankDataChange(e)}/>
        </div>;
    }
}

function AirReaderPieChart(props) {
    let dataToShow = props.data.lil;
    console.log('dataTOshow', dataToShow, props);
    return <Card>
        <CardContent>
            <PieChart width={750} height={230}>

                <Pie data={dataToShow} dataKey='value' fill='#ae6623' cx='50%' cy='50%' outerRadius={100}
                     innerRadius={60} label={true}/>
                <Tooltip/>
            </PieChart>
        </CardContent>
    </Card>;
}

function StackedBarChart(props) {

    return <Card style={{margin: '10px'}}>
        <CardContent>
            <ResponsiveContainer height={320}>
                <BarChart height={500}
                          data={props.data}>
                    <XAxis dataKey="date"/>
                    <YAxis yAxisId='0'/>
                    <Tooltip/>
                    <Legend/>
                    <Bar dataKey='priceTTC' stackId='a' fill="#8884d8"/>
                    <Bar dataKey='fees' stackId='a' fill="#f284d8"/>
                    <Bar dataKey='commissionValue' stackId='a' fill="#3384d8"/>
                </BarChart>
            </ResponsiveContainer>
        </CardContent>
    </Card>;
}

function StackedAreaChart(props) {

    return <Card style={{margin: '10px'}}>
        <CardContent>
            <ResponsiveContainer height={320}>
                <AreaChart height={500}
                           data={props.data}>
                    <XAxis dataKey="date"/>
                    <YAxis yAxisId='0'/>
                    <Tooltip/>
                    <Legend/>
                    <Area dataKey='priceTTC' stackId='a' fill="#8884d8"/>
                    <Area dataKey='fees' stackId='a' fill="#f284d8"/>
                    <Area dataKey='commissionValue' stackId='a' fill="#3384d8"/>
                </AreaChart>
            </ResponsiveContainer>
        </CardContent>
    </Card>;
}

function DateFilterManager(props) {
    let dateInputDatas = [
        {label: 'Date de debut', name: 'beginDate', type: 'date', value: props.beginDate},
        {label: 'Date de fin', name: 'endDate', type: 'date', value: props.endDate},
    ];
    let dateTextfields = dateInputDatas.map(dateData => <TextField
            InputLabelProps={{shrink: true}}
            style={{margin: '20px'}}
            type={dateData.type}
            variant="outlined"
            label={dateData.label}
            name={dateData.name}
            key={dateData.name}
            value={dateData.value}
            placeholder=' '
            onChange={e => props.handleDateChange(e)}
            InputProps={{
                // endAdornment: <InputAdornment position="end">Kg</InputAdornment>,
            }}
        />,
    );
    return <Card style={props.style}>
        <CardContent>
            <div>Filtre Date</div>
            <div className='flex-container'>{dateTextfields}</div>
            {props.children}
        </CardContent>
    </Card>;
}

class BankDatasCard extends React.Component {
    state = {
        result: 0,
    };

    getBankDatas() {
        let bankDatasFromProps = this.props.bankDatas;
        let result = bankDatasFromProps.iata - bankDatasFromProps.scb - bankDatasFromProps.ecobank - bankDatasFromProps.cashier - bankDatasFromProps.BOs;
        return [
            {label: 'Facture IATA', name: 'iata', type: 'number'},
            {label: 'Solde SCB', name: 'scb', type: 'number'},
            {label: 'Solde Ecobank', name: 'ecobank', type: 'number'},
            {label: 'Solde caisse', name: 'cashier', type: 'number'},
            {label: 'Decouvert SCB', name: 'scbDecouvert', type: 'number'},
            {label: 'BO constitus', name: 'BOs', type: 'number'},
            {label: 'Reste  trouver / constituer', name: 'result', type: 'number', value: result, withColor: true},
        ];
    }

    generateBankDatasFields(bankDatas = this.getBankDatas()) {
        return bankDatas.map(bankData => {
            let bankDataValue = bankData.value ? bankData.value : this.props.bankDatas[bankData.name];
            let colorInputLabelProps = bankData.withColor ? (bankDataValue < 0 ? 'green' : 'red') : '';
            return <TextField
                style={{margin: '20px'}}
                variant="outlined"
                type={bankData.type}
                label={bankData.label}
                name={bankData.name}
                key={bankData.name}
                value={bankDataValue}
                placeholder=' '
                onChange={e => this.props.handleChange(e)}
                InputProps={{
                    endAdornment: <InputAdornment position="end">FCFA</InputAdornment>,
                }}
                InputLabelProps={{style: {color: colorInputLabelProps}}}
            />;
        });
    }

    render() {
        return <Card className={this.props.className} style={{marginBottom: '10px'}}>
            <CardContent>
                {this.generateBankDatasFields()}
            </CardContent>
        </Card>;
    }
}


