import React, { Component } from 'react';
import { ListElement} from "../add_customer_account/showCustomerAccounts";
import { numbToLetters } from "../useful_functions/numbToLetters";
import { billServerService } from "../server_classes/serverData";
import { BillDisplay } from "../bill_maker/bill_display";
import { ListElementSelector } from "../add_customer_account/ListElementSelector";


export class AccountStatement extends Component {
    VALIDATE_BILL = "Validate bill";
    VALIDATION_IN_PROGRESS = "En cours";
    VALIDATION_DONE = 'Validation effectue';
    VALIDATION_ERROR = 'Erreur lors de la validation';

    constructor(props) {
        super(props);
        this.state = {
            accountName: '',
            accountsSelected: [],
            accountToBill: '',
            ticketsSelected: [],
            keyToResearch: '',
            valueToResearch: '',
            selected: [],
            multiChoice: this.props.multiChoice,
            billNumber: '',
            commandNumber: '',
            remarks: '',
            isValidateBillButtonDisabled: true,
            validateButtonMessage: this.VALIDATE_BILL,
            dateToShow: '',
            billTotalMoneyNumbers: {
                totalToPay: 0,
                fees: 0,
                discount: 0,
                TVA: 0,
                netToPay: 0,
                netToPayInLetters: numbToLetters(0),
            },
            billToValidate: {},
            billCreationStage: 0,
            valueToBill: 'fees',
            dateBeginFilterInputValue: '000000',
            dateEndFilterInputValue: '999999'
        }
    }


    onAccountSelected(accountsSelected) {
        console.log("accountToBill ad", accountsSelected, Array.isArray(accountsSelected), accountsSelected.length);
        this.setState({
            accountsSelected: accountsSelected,
            ticketsSelected: [],
        });
        this.setBillCreationStage(1);
        if (accountsSelected.length === 1) {
            setTimeout(
                () => {
                    this.onAccountToBillSelected(accountsSelected[0])
                }
                , 100)
        }
        billServerService.getNextBillNumber().then(billNumber => this.setState({
            billNumber: billNumber,
            isValidateBillButtonDisabled: false,
            validateButtonMessage: this.VALIDATE_BILL
        }));
    }

    onAccountToBillSelected(accountToBillSelected) {
        this.setState({
            accountToBill: accountToBillSelected,
        });
        this.setBillCreationStage(2);
        let ticketSelected = this.props.allTickets.filter(x => this.stateAccountFilterFunction(x, this));
        this.onTicketSelected(ticketSelected);
    }

    unselectAccount() {
        this.setBillCreationStage(0);
        this.setState({
            accountToBill: ''
        })
    }

    setBillCreationStage(value) {
        this.setState({
            billCreationStage: value
        })
    }

    stateAccountFilterFunction(x, self) {
        let accountSelectedIds = self.state.accountsSelected.map(x => x['_id']);
        // console.log(accountSelectedIds, x['customerId'], x['customerId'] in accountSelectedIds);
        return x['checked'] === true &&
            accountSelectedIds.includes(x['customerId']) &&
            x['date'] >= self.state.dateBeginFilterInputValue &&
            x['date'] <= self.state.dateEndFilterInputValue &&
            !x['canceled'];
    }

    filterFunction(x, self) {
        return x['checked'] === true &&
            x['customerId'] === self.state.accountToBill['_id'] &&
            !x['canceled'] &&
            !Object.keys(x).includes('billed');
    }

    onTicketSelected(ticketsSelected) {
        console.log("ticketsSelected", ticketsSelected);

        let totalToPay = ticketsSelected.reduce((sum, current) => sum + parseInt(current[this.getValueToBill()], 10), 0);
        let fees = 0;
        let discount = 0;
        let TVA = 0;
        let netToPay = totalToPay + fees + discount + TVA;
        let billTotalMoneyNumbers = {
            totalToPay: totalToPay,
            fees: 0,
            discount: 0,
            TVA: 0,
            netToPay: totalToPay + fees + discount + TVA,
            netToPayInLetters: numbToLetters(netToPay),
        };
        billServerService.getNextBillNumber().then(billNumber => this.setState({
            billNumber: billNumber
        }));
        this.setState({
            ticketsSelected: ticketsSelected,
            billTotalMoneyNumbers: billTotalMoneyNumbers,
            isValidateBillButtonDisabled: false,
            validateButtonMessage: this.VALIDATE_BILL
        });
        this.setBillCreationStage(2)
    }

    handleInputChange(event) {
        let target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;

        this.setState({
            [event.target.name]: value,
        });
    }

    getValueToBill() {
        if (this.state.valueToBill) {
            return this.state.valueToBill;
        }
        return this.state.accountToBill['with FS'] ? 'total' : 'priceTTC'
    }

    setValueToBill(valueToBill) {
        this.setState({
            valueToBill: valueToBill
        })
    }

    generateBill(ticketsToBill, netToPay) {

        let billToInsert = {
            tickets: ticketsToBill.map(ticket => [ticket['_id'], parseInt(ticket[this.getValueToBill()], 10)], this.state.valueToBill),
            customerAccount: this.state.accountToBill['_id'],
            billNumber: this.state.billNumber,
            commandNumber: this.state.commandNumber,
            totalBilled: netToPay,
            remarks: this.state.remarks,
            valueBilled: this.getValueToBill(),
            creationDate: new Date().getTime(),
        };

        this.insertBillToDatabase(billToInsert, this);
    }

    handleDateFilterInputChange(event) {
        // let valueToSet = event.target.value;
        let dateObtained = event.target.value.split('-');
        dateObtained[0] = dateObtained[0].substring(2);
        let valueToSet = dateObtained.join('');
        console.log(valueToSet);
        this.setState({
            [event.target.name]: valueToSet
        })
        // this.onTicketSelected(this.getTicketFiltered());
    }

    insertBillToDatabase(billToInsert, self) {
        console.log(billToInsert);
        this.setState({
            isValidateBillButtonDisabled: true,
            validateButtonMessage: this.VALIDATION_IN_PROGRESS
        });

        billServerService.insertBill(billToInsert)
            .then(function (response) {
                console.log(response);
                self.setState({
                    validateButtonMessage: self.VALIDATION_DONE,
                    dateToShow: billToInsert.creationDate
                });
                response['ticketsUpdated'].map(ticket => self.props.updateTicket(ticket));
            }).catch(function (error) {
            self.setState({
                validateButtonMessage: self.VALIDATION_ERROR,
                dateToShow: billToInsert.creationDate
            });
            console.log(error)
        })
    }

    getTicketFiltered() {
        return this.props.allTickets.filter(x => this.stateAccountFilterFunction(x, this));

    }

    render() {
        let dataToShow = this.props.allTickets.filter(x => this.filterFunction(x, this));
        let ticketsToBill = this.getTicketFiltered();

        let totalToPay = ticketsToBill.reduce((sum, current) => sum + parseInt(current[this.getValueToBill()], 10), 0);
        let fees = 0;
        let discount = 0;
        let TVA = 0;
        let netToPay = totalToPay + fees + discount + TVA;
        let billTotalMoneyNumbers = {
            totalToPay: totalToPay,
            fees: 0,
            discount: 0,
            TVA: 0,
            netToPay: totalToPay + fees + discount + TVA,
            netToPayInLetters: numbToLetters(netToPay),
        };

        return <div className="flex-container flex-direction-column">
            <div className="flex-container flex-direction-column margin-bottom-20px ">
                <div className="margin-left-right-auto margin-bottom-20px">
                    <div onClick={() => this.unselectAccount()}>Choisir compte</div>
                    <ListElementSelector onClick={x => console.log(x)}
                                         onValid={accountSelected => {
                                             this.onAccountSelected(accountSelected);
                                         }}
                                         data={this.props.allAccount}
                                         dataKeysToShow={['name', 'customer reference']}
                                         filterPossibilities={['name', 'customer reference']}
                                         multiChoice={true}
                                         className={this.state.billCreationStage === 0 ? '' : 'displayNone'}
                    />
                    <div className={this.state.billCreationStage === 1 ? '' : 'displayNone'}>
                        Choisir le compte  facturer
                        <ListElement dataToRender={this.state.accountsSelected}
                                     onElementClick={(element) => this.onAccountToBillSelected(element)}
                                     dataKeysToShow={['name', 'customer reference']}

                        />
                    </div>
                    <div className={this.state.billCreationStage > 1 ? '' : 'displayNone'}>
                        Compte de facturation
                        <div className="text-left margin-bottom-20px">
                            <div>{this.state.accountToBill['name']}</div>
                            <div>{this.state.accountToBill['customer reference']}</div>
                            <div>{this.state.accountToBill['adress']}</div>
                            <div>{this.state.accountToBill['city'] + ' - ' + this.state.accountToBill['country']}</div>
                            <div>{this.state.accountToBill['entire name']}</div>
                            <div>{"Avec FS : " + this.state.accountToBill['with FS']}</div>
                            <div>{"Type de paiement : " + this.state.accountToBill['paiement type']}</div>
                        </div>
                        <div className={this.state.accountsSelected.length > 1 ? '' : 'displayNone'}>
                            Comptes prsents dans les etats
                            <ListElement dataToRender={this.state.accountsSelected}
                                         onElementClick={(element) => console.log(element)}
                                         dataKeysToShow={['name', 'customer reference']}

                            />
                        </div>
                    </div>
                </div>

                <div
                    className={'margin-left-right-auto margin-bottom-20px ' + (this.state.accountToBill ? '' : 'displayNone')}>
                    <div>
                        <label>Date de dbut</label>
                        <input placeholder="Jour" type="date" name="dateBeginFilterInputValue"
                               onChange={event => this.handleDateFilterInputChange(event)}/>
                    </div>
                    <div>
                        <label>Date de fin</label>
                        <input placeholder="Jour" type="date" name="dateEndFilterInputValue"
                               onChange={event => this.handleDateFilterInputChange(event)}/>
                    </div>
                    <div>
                        {[{
                            textToShow: 'billet normal',
                            valueToBill: this.state.accountToBill['with FS'] ? 'total' : 'priceTTC'
                        },
                            {textToShow: 'frais de service', valueToBill: 'fees'},].map(x =>
                            <div key={x.textToShow}
                                 className={this.state.valueToBill === x.valueToBill ? 'bold-text' : ''}
                                 onClick={() => this.setValueToBill(x.valueToBill)}>
                                {x.textToShow}
                            </div>
                        )}
                    </div>
                </div>


            </div>

            <div className="flex-container padding-20px margin-20-px">
                <div>

                    <div
                        className={'margin-left-right-auto text-left ' + (this.state.ticketsSelected.length > 0 ? '' : 'displayNone')}>
                        <div>
                            <label>Facture n : </label>
                            <input name='billNumber' value={this.state.billNumber}
                                   onChange={(e) => this.handleInputChange(e)}/>
                        </div>
                        <div>
                            <label>Bon de commande : </label>
                            <input name='commandNumber' onChange={(e) => this.handleInputChange(e)}/>
                        </div>
                        <div>
                            <label className="align-top">Remarques : </label>
                            <textarea id="remark-field-bill-maker" name='remarks'
                                      onChange={(e) => this.handleInputChange(e)}/>
                        </div>

                    </div>

                    <button className={this.state.ticketsSelected.length > 0 ? '' : 'displayNone '}
                            onClick={() => this.generateBill(ticketsToBill,netToPay)}
                            disabled={this.state.isValidateBillButtonDisabled}>
                        {this.state.validateButtonMessage}
                    </button>
                </div>

                <BillDisplay accountSelected={this.state.accountToBill}
                             ticketsSelected={ticketsToBill}
                             dateToShow={this.state.dateToShow}
                             billNumber={this.state.billNumber}
                             commandNumber={this.state.commandNumber}
                             remarks={this.state.remarks}
                             valueToBill={this.state.valueToBill}
                    // valueToBill={this.state.accountToBill['with FS'] ? 'total' : 'priceTTC'}
                             showDetailsData={true}
                             className="flex-1-1"
                />
            </div>

        </div>
    }
}
