import Dialog from "@material-ui/core/es/Dialog/Dialog";
import DialogTitle from "@material-ui/core/es/DialogTitle/DialogTitle";
import DialogContent from "@material-ui/core/es/DialogContent/DialogContent";
import React from 'react';
import DialogActions from "@material-ui/core/es/DialogActions/DialogActions";
import Button from "@material-ui/core/es/Button/Button";
import ReactJson from 'react-json-view'


export default class ModifyObjectDialog extends React.Component {
    static defaultProps = {
        editable: true,
    }

    constructor() {
        super();
        this.state = {
            elementModified: null,
            connexionRunning: false,
        }
    }

    handleClose() {
        this.setState({
            elementModified: null,
        });
        this.props.onClose();
    }

    handleValidation() {
        this.props.onValidate ? this.props.onValidate(this.state.elementModified, () =>this.handleClose()) : this.handleClose();
    }

    setConnexionRunning(bool) {
        this.setState({connexionRunning: bool})
    }

    modifyObjectOnServer() {
        this.setConnexionRunning(true);

    }

    onEnterClick(event) {
        if (event.key === 'Enter') {
            this.handleValidation()
        }
    }

    render() {
        return <Dialog
            open={this.props.open}
            onClose={() => this.handleClose()}
            onKeyPress={(event) => this.onEnterClick(event)}
            aria-labelledby="form-dialog-title"
            fullWidth
        >
            <DialogTitle id="form-dialog-title">{this.props.title}</DialogTitle>
            <DialogContent>
                {this.props.editable ?
                    <ReactJson indentWidth={10}
                               enableClipboard={false}
                               onEdit={x => this.setState({elementModified: x.updated_src})}
                               src={this.props.objectToModify}
                               theme="rjv-default"/>
                    : <ReactJson indentWidth={10}
                                 enableClipboard={false}
                                 src={this.props.objectToModify}
                                 theme="rjv-default"/>}
            </DialogContent>
            <DialogActions>
                <Button onClick={() => this.handleValidation()}
                        color="primary"
                        disabled={this.state.connexionRunning}>
                    Valider
                </Button>
            </DialogActions>
        </Dialog>
    }
}

