import React, { Component } from 'react';
import './superDataTable.css'
import { ElementsSelector } from "./ElementsSelector";
import SimpleMenu from "./SimpleMenu";
import ModifyObjectDialog from "../Dialogs/ModifyObjectDialog";


export class EasyDataTable extends Component {
    constructor(props) {
        super(props);
        //Props dataToShow, keysToShow = {'name': }
        this.state = {
            // dataToShow: [],
            shouldUpdateDataTableData: false,
            currentKeysToShowObject: '',
            initialKeysToShow: '',
            selectorDatas: {X: 0, Y: 0, display: 'none', validateFilterValues: x => console.log(x), datas: []},
            sortBy: {},
            filterBy: {},
            simpleMenuAnchorEl: null,
            elementToModify: null,
            modifyElementModalOpen: false,
        }
    }

    componentWillReceiveProps(nextProps) {
        // You don't have to do this check first, but it can help prevent an unneeded render
        if (nextProps.startTime !== this.state.startTime) {
            this.setState({startTime: nextProps.startTime});
        }
        this.refreshDataTableData(nextProps);
    }


    getDataKeysToShowFromData(data) {
        let dataKeysSet = new Set();
        for (let dataObject of data) {
            for (let dataObjectKey of Object.keys(dataObject)) {
                dataKeysSet.add(dataObjectKey)
            }
        }
        return [...dataKeysSet].reduce((final, current) => {
            final[current] = current;
            return final
        }, {})
    }

    setSortBy(keyToSortBy) {
        let newObjectToSortBy = Object.assign({}, this.state.sortBy);
        switch (newObjectToSortBy[keyToSortBy]) {
            case -1 :
                delete newObjectToSortBy[keyToSortBy];
                break;
            case 1 :
                newObjectToSortBy[keyToSortBy] = -1;
                break;
            default:
                newObjectToSortBy[keyToSortBy] = 1;
        }
        this.setState({
            sortBy: newObjectToSortBy
        })
    }

    checkIfSortedByKey(keyToCheck) {
        return Object.keys(this.state.sortBy).includes(keyToCheck)
    }

    getSortingFunction(objectToSortBy) { ///Prend objet du genre : {nom: -1, age: 1}
        let arrayToSortBy = Object.keys(objectToSortBy).filter(x => objectToSortBy[x] !== 0);
        return (a, b) => {
            for (let sortByKey of arrayToSortBy) {
                if (a[sortByKey] === b[sortByKey]) {

                } else {
                    return (a[sortByKey] < b[sortByKey] ? -1 : 1) * objectToSortBy[sortByKey];
                }
            }
            return 0
        }
    }

    setFilterBy(keyToFiler, arrayValuesToFilter) {
        let newFilterBy = Object.assign({}, this.state.filterBy);
        if (arrayValuesToFilter.length === 0) {
            delete newFilterBy[keyToFiler]
        } else {
            newFilterBy[keyToFiler] = arrayValuesToFilter;
        }
        this.setState({filterBy: newFilterBy});
    }

    checkIfFilteredBy(keyToCheck) {
        return !!this.state.filterBy[keyToCheck];
    }

    getFilteringFunction(objectToFilterBy) {
        console.log(objectToFilterBy);
        let arrayKeysToFilterBy = Object.keys(objectToFilterBy)
        return x => {
            for (let keyToFilterBy of arrayKeysToFilterBy) {
                if (!objectToFilterBy[keyToFilterBy].includes(x[keyToFilterBy])) {
                    return false
                }
            }
            return true
        }
    }

    filterButtonClick(e, datakey, dataToShow) {
        e.stopPropagation();
        const rect = e.target.parentNode.parentNode.getBoundingClientRect();

        let filterValuesSet = new Set();
        for (let dataObject of dataToShow) {
            filterValuesSet.add(dataObject[datakey])
        }
        console.log(filterValuesSet);
        this.setState({
            selectorDatas: {
                X: rect.left + window.scrollX,
                Y: rect.bottom + window.scrollY,
                display: 'initial',
                validateFilterValues: arrayValuesToFilter => {
                    this.setFilterBy(datakey, arrayValuesToFilter);
                    this.setState({selectorDatas: {display: 'none'}})
                },
                datas: [...filterValuesSet].sort(),
                selectedDatas: this.state.filterBy[datakey]
            }
        });
    }

    refreshDataTableData(props = this.props) {
        let allDataKeys = this.getDataKeysToShowFromData(props.dataToShow);
        this.setState({
            initialKeysToShow: allDataKeys,
            currentKeysToShowObject: allDataKeys,
        })
    }

    setKeysToShowObject(newKeysToShowObject) {
        this.setState({
            currentKeysToShowObject: newKeysToShowObject
        })
    }

    getSortOrientation(keyToTest) {
        if (this.checkIfSortedByKey(keyToTest)) {
            if (this.state.sortBy[keyToTest] > 0) {
                return <span><i className="fas fa-sort-up"/></span>
            } else {
                return <span><i className="fas fa-sort-down"/></span>
            }
        }
        return <span><i className="fas fa-sort"/></span>
    }


    render() {
        let keysToShow = Object.keys(this.state.currentKeysToShowObject);
        let dataToShow = this.props.dataToShow.filter(this.getFilteringFunction(this.state.filterBy));
        dataToShow = this.props.dataToShow

        dataToShow = dataToShow.sort(this.getSortingFunction(this.state.sortBy));

        let tableHeader = <thead>
        <tr>
            {keysToShow.map((keyToShow, step) =>
                <td key={step}
                    onClick={() => this.setSortBy(keyToShow)}>
                    <div className="flex"><span
                        className="flex-1-1">{this.state.currentKeysToShowObject[keyToShow]}</span>
                        {this.getSortOrientation(keyToShow)}
                        <span onClick={e => this.filterButtonClick(e, keyToShow, this.props.dataToShow)}>
                            <i className="fas fa-filter"
                               style={{color: this.checkIfFilteredBy(keyToShow) ? 'red' : ''}}/>
                        </span>
                    </div>
                </td>)
            }
        </tr>
        </thead>

        let tableRowsToShow = dataToShow.map((dataObject, step) => {
            let valuesOnTicket = keysToShow.map((keyToShow, step) =>
                <td key={step}>
                    {typeof dataObject[keyToShow] === 'object' ? 'object' : dataObject[keyToShow]}
                </td>);
            return <tr
                key={step}
                onClick={event => this.props.onElementClick(event,dataObject)}>
                {valuesOnTicket}
            </tr>
        });

        let tableToShow = <div id="superDataTableDiv">
            <table id="superDataTable" className="text-left">
                {tableHeader}
                <tbody>
                {tableRowsToShow}
                </tbody>
            </table>
        </div>

        return <div>
            <ElementsSelector selectorDatas={this.state.selectorDatas}/>
            {tableToShow}
        </div>;
    }
}