import React, { Component } from 'react';
import './bill_display.css'
import { numbToLetters } from "../useful_functions/numbToLetters";
import { generateLongDate } from "../useful_functions/generateLongDate";

const FACTURE = 'FACTURE';


export class BillDisplay extends Component {

    constructor(props) {
        super(props);
        this.state = {
            companyDatas: {
                name: 'Agence Franaise de Voyages',
                adress: 'B.P. 245, Yaounde',
                tel: '(+237) 77 93 78 21 - 22',
                mail: 'afv_cameroun@yahoo.fr',
                contribuable_ID: 'N CONTRIBUABLE : P036300272328L'
            }
        }
    }

    getTicketArray() {
        return this.props.ticketsSelected.map(ticket => {
            let flightDate = 'DEP : ' + ticket['flightDates'][0];
            if (ticket['flightDates'].length > 1) {
                flightDate = flightDate + ' RET ' + ticket['flightDates'][1]
            }
            return <tr key={ticket._id} className=" no-border ticket-tr">
                <td className="no-border border-right padding-5px">
                    <div>{ticket['ticketNumber']}</div>
                    <div className={this.props.showDetailsData ? '' : 'displayNone'}>{ticket['date']}</div>
                </td>
                <td className="no-border border-right padding-5px">
                    <div>{ticket['companyName'] + ' : ' + ticket['trip']}</div>
                    <div>{ticket['name']}</div>
                    <div>{flightDate}</div>
                </td>
                {/*<td>{this.props.accountSelected['with FS'] === 'true'? ticket['total'] : ticket['priceTTC']}</td>*/}
                <td className="no-border text-right padding-5px">{parseInt(ticket[this.props.valueToBill], 10).toLocaleString()}</td>
            </tr>
        });
    }

    render() {
        return <div className={this.props.className}>
            <BillContext
                printable = {this.props.printable}
                className={"centeredDiv text-left " + (this.props.accountSelected && this.props.ticketsSelected.length > 0 ? '' : 'displayNone')}
                billType={FACTURE}
                billNumber={this.props.billNumber}
                companyDatas={this.state.companyDatas}
                accountSelected={this.props.accountSelected}
                remarks={this.props.remarks}
                date={this.props.dateToShow}
            >
                <TicketBilling ticketsSelected={this.props.ticketsSelected} valueToBill={this.props.valueToBill}
                               accountSelected={this.props.accountSelected} commandNumber={this.props.commandNumber}
                               billNumber={this.props.billNumber} showDetailsData={this.props.showDetailsData}/>

            </BillContext>
        </div>
    }

}


export function BillCompanyDetails(props) {
    return <div className={props.className}>
        <div className="bold-text all-caps ">{props.companyDatas.name}</div>
        <div>{props.companyDatas.adress}</div>
        <div>{props.companyDatas.tel}</div>
        <div>{props.companyDatas.mail}</div>
        <div>{props.companyDatas.contribuable_ID}</div>
    </div>
}

export function BillCustomerDetails(props) {
    return <div className={props.className}>
        <div>
            <div className="bold-text all-caps customerAccountNameDiv">
                {props.accountSelected['entire name']}
            </div>
            <div>{props.accountSelected['adress']}</div>
            <div>{props.accountSelected['city'] +
            (props.accountSelected['country'] ? ' - ' + props.accountSelected['country'] : '')}</div>
            <div>{props.accountSelected['phone'] ? 'Tel :' + props.accountSelected['phone'] : ''}</div>
        </div>

    </div>
}

export function BillBottom(props) {
    return <div>
        <div className={props.limitDateHidden ?  "displayNone" : "padding-5px "}>Echance : {props.date}</div>
        <div className="white-space-wrap padding-5px">
            {props.remarks ? props.remarks.split(";").map(x => <div key={x}>{x}</div>) : ''}
        </div>
        <div className="flex-container">
            <span className="text-left padding-5px flex-1-1">{props.isCreditNote ? 'Le bnficiaire' : ''}</span>
            <span className="text-right padding-5px"> La Direction</span>
        </div>
    </div>
}

export function BillHead(props) {
    return <div>
        <div className="flex-container">
            <div className="flex-1-1"><img src="/static/AFV Name.png" id="companyLogo" alt="company logo" className=""/>
            </div>
            <div className="all-caps"><span className="bold-text">{props.billType + ' n'}</span> {props.billNumber}
            </div>
        </div>

        <div className="flex-container margin-bottom-20px padding-5px">
            <BillCompanyDetails companyDatas={props.companyDatas} className="flex-1-1 "/>
            <BillCustomerDetails className=" " accountSelected={props.accountSelected}/>
        </div>
    </div>
}

export function BillContext(props) {
    // let dateTimeD = new Date(props.date);
    // let dateToShow = '';
    // if (dateTimeD.length > 0)  {
    //     let months = ['janvier', 'fvrier', 'mars', 'avril', 'mai', 'juin', 'juillet', 'aout', 'septembre', 'octobre', 'novembre', 'dcembre'];
    //     dateToShow = dateTimeD.getDate() + ' ' + months[dateTimeD.getMonth()] + ' ' + dateTimeD.getFullYear();
    // }

    return <div
        id={props.printable ? "bill_preview" : ""}
        className={props.className}>

        <BillHead billType={props.billType}
                  billNumber={props.billNumber}
                  companyDatas={props.companyDatas}
                  accountSelected={props.accountSelected}/>

        <div className="padding-5px ">Yaound, le {generateLongDate(props.date)}</div>

        {props.children}
        <BillBottom isCreditNote={props.isCreditNote} remarks={props.remarks} limitDateHidden = {props.limitDateHidden} date={generateLongDate(props.date)}/>
    </div>
}

export function TicketBilling(props) {
    // ticketsSelected = {this.props.ticketsSelected} valueToBill = {this.props.valueToBill}
    // accountSelected = {this.props.accountSelected} commandNumber = {this.props.commandNumber} billNumber = {this.props.billNumber}
    // ticketsArray = {this.getTicketArray()}

    let ticketsArray = props.ticketsSelected.map(ticket => {
        let flightDate = 'DEP : ' + ticket['flightDates'][0];
        if (ticket['flightDates'].length > 1) {
            flightDate = flightDate + ' RET ' + ticket['flightDates'][1]
        }
        return <tr key={ticket._id} className=" no-border ticket-tr">
            <td className="no-border border-right padding-5px">
                <div>{ticket['ticketNumber']}</div>
                <div className={props.showDetailsData ? '' : 'displayNone'}>{ticket['date']}</div>
            </td>
            <td className="no-border border-right padding-5px">
                <div>{ticket['companyName'] + ' : ' + ticket['trip']}</div>
                <div>{ticket['name']}</div>
                <div>{flightDate}</div>
            </td>
            {/*<td>{this.props.accountSelected['with FS'] === 'true'? ticket['total'] : ticket['priceTTC']}</td>*/}
            <td className="no-border text-right padding-5px">{parseInt(ticket[props.valueToBill], 10).toLocaleString()}</td>
        </tr>
    });

    let totalToPay = props.ticketsSelected.reduce((sum, current) => sum + parseInt(current[props.valueToBill], 10), 0);
    let fees = 0;
    let discount = 0;
    let TVA = 0;
    let netToPay = totalToPay + fees + discount + TVA;
    let netToPayInLetters = numbToLetters(netToPay);

    return <div>
        <table className="ticketBillDisplay">
            <thead className="my-header align-top">
            <tr>
                <td className="padding-5px border-right">
                    <div>Bon de commande</div>
                    <div>{props.commandNumber}</div>
                </td>
                <td className="padding-5px border-right">
                    <div>Reference client : {props.accountSelected['customer reference']}</div>
                    <div>Compte : {props.accountSelected['customer account']}</div>
                </td>
                <td className="padding-5px ">{props.billNumber}</td>
            </tr>
            <tr>
                <th className="padding-5px width-150px">Reference</th>
                <th className="padding-5px">Designation</th>
                <th className="padding-5px width-130px">Montant</th>
            </tr>
            </thead>
            <tbody className="align-top">
            {ticketsArray}
            </tbody>
        </table>

        <div className="flex-container page-break">
            <div className="flex-3-1">

            </div>
            <div className="no-padding">
                <table>
                    <tbody>
                    {[
                        {textToShow: 'Total', valueToShow: totalToPay},
                        {textToShow: 'Frais de service', valueToShow: fees},
                        {textToShow: 'Remise', valueToShow: discount},
                        {textToShow: 'TVA', valueToShow: TVA},
                        {textToShow: 'Net  payer (F CFA)', valueToShow: netToPay},
                    ].map(x =>
                        <tr key={x.textToShow}>
                            <td className="width-150px border-all">{x.textToShow}</td>
                            <td className="width-130px text-right border-all padding-5px">{x.valueToShow.toLocaleString()}</td>
                        </tr>)}
                    </tbody>
                </table>
            </div>
        </div>
        <div className="flex-container flex-direction-column padding-5px">
            <div
                className="all-caps bold-text">{netToPayInLetters}</div>
            <div className="flex-3-1">
                Mode de paiement : {props.accountSelected['paiement type']}
            </div>
        </div>


    </div>
}