import React, { Component } from 'react';
import './air-reader-modal.css'

export class AirReaderModal extends Component {

    render() {

        let modalVisibility = this.props.visible ? '' : ' displayNone';

        return <div className={"modal-base" + modalVisibility} onClick={()=>this.props.onOutsideClick()}>
            <div className="modal-body " onClick={(e)=>e.stopPropagation()}>
                {this.props.children}
            </div>
        </div>
    }

}