import React, { Component } from 'react';
import { ListElement} from "../add_customer_account/showCustomerAccounts";
import { CreditNoteServerService } from "../server_classes/creditNoteServerRequest";
import { AccountDetails } from "../add_customer_account/accountDetails";
import { CreditNoteBill } from "./CreditNoteBill";
import { ListElementSelector } from "../add_customer_account/ListElementSelector";


export class CreditNoteBillViewer extends Component {
    constructor(props) {
        super(props);
        this.state = {
            billCreationStage: 0,
            accountSelected: '',
            creditNoteBillSelected: '',
            creditNotesSelected: [],
            creditNoteNumber: '',
            billTotalMoneyNumbers: '',
            isValidateBillButtonDisabled: '',
            validateButtonMessage: '',
            commandNumber: '',
            remarks: '',
            dateToShow: '',
            paymentType: '',
        }
    }

    onAccountSelected(accountSelected) {
        console.log("accountSelected", accountSelected);
        this.setState({
            accountSelected: accountSelected,
            creditNotesSelected: [],
            paymentType: accountSelected['paiement type'],
        });
        this.setBillCreationStage(1)
    }

    onCreditNoteBillSelected(creditNoteBillSelected) {
        console.log("creditNoteBillSelected", creditNoteBillSelected);
        this.setState({
            creditNoteBillSelected: creditNoteBillSelected,
            creditNotesSelected: [],
            paymentType: creditNoteBillSelected['paiement type'],
            dateToShow: creditNoteBillSelected.creationDate,

        });
        this.findCreditNotesFromBill(creditNoteBillSelected);
        // this.findAccountBilledFromBill(creditNoteBillSelected);
        this.setBillCreationStage(2);
    }

    findCreditNotesFromBill(creditNoteBill) {
        let creditNotesId = creditNoteBill.credit_notes.map(x => x[0]);
        let creditNotesOnBill = this.props.allCreditNotes.filter(creditNote => creditNotesId.includes(creditNote._id));
        console.log(creditNotesOnBill);
        this.setState({
            creditNotesSelected: creditNotesOnBill,
        })
    }

    findAccountBilledFromBill(creditNoteBill) {
        this.setState({
            accountSelected: this.props.allAccount.find(x => x._id === creditNoteBill.customerId),
        })
    }

    setBillCreationStage(value) {
        this.setState({
            billCreationStage: value
        })
    }

    unselectAccount() {
        this.setBillCreationStage(0);
        this.setState({
            accountSelected: ''
        })
    }

    unselectCreditNoteBill() {
        this.setBillCreationStage(1);
        this.setState({
            creditNoteBillSelected: '',
            creditNotesSelected: [],
            paymentType: '',
        });
    }

    filterFunction(x) {
        return !x['canceled'] && x.customerId === this.state.accountSelected._id
    }

    sendCancelCreditNoteBillRequest(creditNoteBillToDelete, self = this) {
        CreditNoteServerService.cancelCreditNoteBillRequest(creditNoteBillToDelete).then(creditNoteBillReturned => {
            if (!creditNoteBillReturned) return;
            console.log('creditNoteBillReturned', creditNoteBillReturned);
            self.props.cancelCreditNoteBill(creditNoteBillReturned['credit_note_bill'], creditNoteBillReturned['credit_notes_updated']);
            self.unselectCreditNoteBill();
        })
    }

    render() {
        let dataToShow = this.props.allCreditNoteBills.filter(x => this.filterFunction(x, this));
        console.log(dataToShow);

        return <div className="flex-container flex-direction-column ">
            <div className="margin-left-right-auto">
                <div onClick={() => this.unselectAccount()}>Choisir compte</div>
                <ListElementSelector onClick={x => console.log(x)}
                                     onValid={accountSelected => {
                                         this.onAccountSelected(accountSelected);
                                     }}
                                     data={this.props.allAccount}
                                     sortBy='name'
                                     dataKeysToShow={['name', 'customer reference']}
                                     filterPossibilities={['name', 'customer reference']}
                                     multiChoice={false}
                                     className={this.state.billCreationStage === 0 ? '' : 'displayNone'}
                />
                <AccountDetails accountToShow={this.state.accountSelected}
                                className={this.state.billCreationStage > 0 ? '' : 'displayNone'}/>
            </div>


            <div className={(this.state.billCreationStage > 0 ? '' : 'displayNone ') + "margin-left-right-auto"}>
                <div onClick={() => this.unselectCreditNoteBill()}>Selectionner factures</div>
                <ListElementSelector onClick={x => console.log(x)}
                                     onValid={accountSelected => {
                                         this.onCreditNoteBillSelected(accountSelected);
                                     }}
                                     data={dataToShow}
                                     dataKeysToShow={['billNumber', 'customer reference']}
                                     filterPossibilities={['billNumber', 'customer reference']}
                                     multiChoice={false}
                                     className={this.state.billCreationStage === 1 ? '' : 'displayNone'}
                />
                <ListElement dataToRender={[this.state.creditNoteBillSelected]}
                             onElementClick={(element) => console.log(element)}
                             dataKeysToShow={['creationDate', 'passengerName', 'initialTicketAmount', 'netToRefund']}
                             className={this.state.billCreationStage > 1 ? '' : 'displayNone'}
                />
            </div>

            <div className="flex-container padding-20px margin-20-px">
                <CreditNoteBill printable={true}
                                accountSelected={this.state.accountSelected}
                                creditNotes={this.state.creditNotesSelected}
                                dateToShow={this.state.dateToShow}
                                billNumber={this.state.creditNoteBillSelected.billNumber}
                                commandNumber={this.state.creditNoteBillSelected.commandNumber}
                                remarks={this.state.creditNoteBillSelected.remarks}
                                valueToBill={this.state.valueToBill}
                                showDetailsData={true}
                                paymentType={this.state.creditNoteBillSelected.paymentType}
                                className="flex-1-1">

                </CreditNoteBill>
            </div>

            <div className={this.state.creditNoteBillSelected ? '' : 'displayNone'}>
                <button onClick={() => this.sendCancelCreditNoteBillRequest(this.state.creditNoteBillSelected)}>
                    Supprimer avoir ?
                </button>
            </div>

        </div>
    }
}