class BOSheet extends Component {
    constructor(props) {
        super(props);
        this.state = {
            date: new Date().toLocaleDateString(),
            dateX: 0,
            dateY: 0,
            date2X: 0,
            date2Y: 0,
            dateEnd: new Date().toLocaleDateString(),
            dateEndX: 0,
            dateEndY: 0,
            customer: '',
            customerX: 0,
            customerY: 0,
            adress: '',
            amount: 0,
            amountX: 0,
            amountY: 0,
            amountInLetter: '',
            amountInLetterX: 0,
            amountInLetterY: 0,
            imgContainer: '',
            timeOutToPositionBO: '',
        }
        this.imgDiv = React.createRef();
    }

    componentDidMount() {
        this.setState({imgContainer: <img ref={this.imgDiv} src='/static/Fiche BO Vierge.jpg' style={{width: '1000px'}} onClick={e => {
            let rect = e.target.getBoundingClientRect();
            this.setPosition(rect);
        }}/>});
        let timeOutToPositionBO =setTimeout(()=>this.imgDiv.current.click(), 1000);
        this.setState({timeOutToPositionBO:timeOutToPositionBO})
    }

    onInputChange(e) {
        this.setState({
            [e.target.name]: e.target.value
        });
        if (e.target.name === 'amount') {
            this.setState({amountInLetter: numbToLetters(parseInt(e.target.value))})
        }
    }

    componentWillUnmount() {
        clearTimeout(this.state.timeOutToPositionBO)
        // console.log('unmounting');
    }

    setPosition(rect) {
        this.setState({
            dateX: (rect.right - rect.left) / 2 - 20,
            dateY: rect.top + window.scrollY + 104,
            date2X: (rect.right - rect.left) / 2 - 140,
            date2Y: rect.top + window.scrollY + 150,
            dateEndX: (rect.right - rect.left) / 2 - 120,
            dateEndY: rect.top + window.scrollY + 170,
            amountX: (rect.left + rect.right)* 3 / 4,
            amountY: rect.top + window.scrollY + 168,
            amountInLetterX: (rect.left + rect.right) / 2 - 70,
            amountInLetterY: rect.top + window.scrollY + 330,
            customerX: (rect.left + rect.right) / 2 + 30,
            customerY: rect.top + window.scrollY + 363,
        });
    }

    render() {
        console.log(this.state.imgContainer);
        console.log(this.imgDiv);
        let boToShow = this.props.boToShow ? this.props.boToShow : {
            customer: this.state.customer,
            amount: this.state.amount,
            date: this.state.date,
            dateEnd: this.state.dateEnd,
        };


        return <div>
            <div>
                <input value={this.state.date} onChange={(e) => this.onInputChange(e)} name='date' placeholder="date"/>
                <input value={this.state.dateX} onChange={(e) => this.onInputChange(e)} name='dateX'
                       placeholder="dateX"/>
                <input value={this.state.dateY} onChange={(e) => this.onInputChange(e)} name='dateY'
                       placeholder="dateY"/>
            </div>
            <div>
                <input value={this.state.dateEnd} onChange={(e) => this.onInputChange(e)} name='dateEnd' placeholder="dateEnd"/>
                <input value={this.state.dateEndX} onChange={(e) => this.onInputChange(e)} name='dateEndX'
                       placeholder="dateEndX"/>
                <input value={this.state.dateEndY} onChange={(e) => this.onInputChange(e)} name='dateEndY'
                       placeholder="dateEndY"/>
            </div>
            <div>
                <input value={this.state.amount} onChange={(e) => this.onInputChange(e)} name='amount'
                       placeholder="amount"/>
                <input value={this.state.amountX} onChange={(e) => this.onInputChange(e)} name='amountX'
                       placeholder="amountX"/>
                <input value={this.state.amountY} onChange={(e) => this.onInputChange(e)} name='amountY'
                       placeholder="amountY"/>
            </div>
            <div>
                <input value={this.state.amountInLetter} onChange={(e) => this.onInputChange(e)} name='amountInLetter'
                       placeholder="amountInLetter"/>
                <input value={this.state.amountInLetterX} onChange={(e) => this.onInputChange(e)} name='amountInLetterX'
                       placeholder="amountInLetterX"/>
                <input value={this.state.amountInLetterY} onChange={(e) => this.onInputChange(e)} name='amountInLetterY'
                       placeholder="amountInLetterY"/>
            </div>
            <div>
                <input value={this.state.customer} onChange={(e) => this.onInputChange(e)} name='customer'
                       placeholder="client"/>
                <input value={this.state.customerX} onChange={(e) => this.onInputChange(e)} name='customerX'
                       placeholder="clientX"/>
                <input value={this.state.customerY} onChange={(e) => this.onInputChange(e)} name='customerY'
                       placeholder="clientY"/>
            </div>
            <input value={this.state.amount} onChange={(e) => this.onInputChange(e)} name='amount'
                   placeholder="amount"/>
            <input value={this.state.date} onChange={(e) => this.onInputChange(e)} name='date' placeholder="date"/>
            <div className=" printable flex-container-column boPaperToPrint">
                {this.state.imgContainer}
                <div style={{position: 'absolute', left: parseInt(this.state.dateX), top: parseInt(this.state.dateY)}}>
                    {boToShow.date}
                </div>
                <div style={{position: 'absolute', left: parseInt(this.state.date2X), top: parseInt(this.state.date2Y)}}>
                    {'Du ' + boToShow.date}
                </div>
                <div style={{position: 'absolute', left: parseInt(this.state.dateEndX), top: parseInt(this.state.dateEndY)}}>
                    {boToShow.dateEnd}
                </div>
                <div style={{
                    position: 'absolute',
                    left: parseInt(this.state.amountX),
                    top: parseInt(this.state.amountY)
                }}>
                    {'# ' + parseInt(boToShow.amount).toLocaleString() + ' #'}
                </div>
                <div style={{
                    position: 'absolute',
                    left: parseInt(this.state.amountInLetterX),
                    top: parseInt(this.state.amountInLetterY),
                    fontSize: '16px',
                    maxWidth: '535px',
                }}>
                    {numbToLetters(parseInt(boToShow.amount))}
                </div>
                <div style={{
                    position: 'absolute',
                    left: parseInt(this.state.customerX),
                    top: parseInt(this.state.customerY),
                }}>
                    {boToShow.customer}
                </div>
            </div>
        </div>
    }

}



