import React, { Component } from "react";
import cloneDeep from "lodash/cloneDeep";
import { sortFunctions } from "../useful_functions/sortFunctions";
import { ListElement } from "./showCustomerAccounts";
import SimpleMenu from "../states_by_date/SimpleMenu";
import ModifyObjectDialog from "../Dialogs/ModifyObjectDialog";
import TextField from "@material-ui/core/es/TextField/TextField";

export class ListElementSelector extends Component {
    constructor(props) {
        super(props);
        this.state = {
            keyToResearch: '',
            valueToResearch: '',
            selected: [],
            multiChoice: this.props.multiChoice,
            simpleMenuAnchorEl: null,
            elementToModify: null,
            modifyElementModalOpen: false,
        }
    }


    filterAction(listElementToFilter, keys, valueToContain) {
        return listElementToFilter.filter(element => {
            for (let key of keys) {
                if (key in element && element[key].indexOf(valueToContain) > -1) {
                    return true
                }
            }
        });
    }

    onResearchValueChange(event) {
        this.setState({
            valueToResearch: event.target.value.toUpperCase(),
        })
    }

    onResearchElementChange(event) {
        this.setState({
            keyToResearch: event.target.value
        })
    }

    handleClickOnElement(elementClicked) {
        let elementSelectionned = this.state.selected;
        console.log(this.state.multiChoice, elementSelectionned);
        if (this.state.multiChoice) {
            if (elementSelectionned.includes(elementClicked)) {
                elementSelectionned.splice(elementSelectionned.indexOf(elementClicked), 1);
            } else {
                elementSelectionned.push(elementClicked);
            }
            this.setState({
                selected: elementSelectionned,
            });
            console.log(this.state.selected);
            //this.props.onClick(elementClicked)
        } else {
            this.setState({
                selected: [elementClicked]
            });
            this.props.onValid(elementClicked)
        }
    }


    selectElements(elementsToSelect) {
        this.setState({
            selected: cloneDeep(elementsToSelect),
        });
    }

    validateSelection() {
        this.props.onValid(this.state.selected);
        this.setState({
            selected: []
        })
    }

    render() {
        let dataToRender = this.props.sortBy ? this.props.data.sort(sortFunctions.sortAlphabaticallyWithKey(this.props.sortBy)) : this.props.data;
        if (this.state.valueToResearch) {
            dataToRender = this.filterAction(dataToRender, this.props.filterPossibilities, this.state.valueToResearch)
        }

        let dataRepresentation =
            <ListElement
                dataToRender={dataToRender}
                onElementClick={(element) => this.handleClickOnElement(element)}
                onElementContextMenu={(event, dataObject) => {
                    console.log('click droit element', event);
                    event.preventDefault();
                    this.setState({simpleMenuAnchorEl: event.target, elementToModify: dataObject})
                }}
                dataKeysToShow={this.props.dataKeysToShow}
                dataAlreadySelected={this.state.selected}
            />;

        return <div className={'margin-left-right-auto ' + this.props.className}>
            {this.state.multiChoice ?
                <div>
                    <button onClick={() => this.validateSelection()}>Valider</button>
                    <button onClick={() => this.selectElements(dataToRender)}>Selectionner Tout</button>
                </div>
                : ''
            }

            <div>
                <div>
                    <label>Filtr selon {JSON.stringify(this.props.filterPossibilities)}</label>
                </div>
                <div>
                    <TextField
                        autoFocus
                        margin="dense"
                        name="username"
                        label="Valeur cherchee"
                        type="text"
                        onChange={(event) => this.onResearchValueChange(event)}
                    />
                </div>

                <SimpleMenu anchorEl={this.state.simpleMenuAnchorEl}
                            handleClose={() => {
                                this.setState({simpleMenuAnchorEl: null, elementToModify: null});
                            }}
                            menuItems={[{
                                text: "Afficher element",
                                action: () => this.setState({modifyElementModalOpen: true, simpleMenuAnchorEl: null})
                            }]}
                />

                <ModifyObjectDialog open={this.state.modifyElementModalOpen}
                                    onClose={() => this.setState({modifyElementModalOpen: false})}
                                    title="Modifier objet"
                                    objectToModify={this.state.elementToModify}
                                    editable={false}

                />

            </div>
            {dataRepresentation}
        </div>
    }
}