import React, { Component } from 'react';
import { EasyDataTable } from "../states_by_date/EasyDataTable";
import { ServerService } from "../server_classes/ServerService";
import {
    ACCOUNTS_PATH,
    BILLS_PATH,
    CREDIT_NOTES_BILL_PATH,
    CREDIT_NOTES_PATH,
    TICKETS_PATH
} from "../server_classes/SERVER_CONST";
import Button from "@material-ui/core/es/Button/Button";
import { AirReaderModal } from "../air_reader_modal/air_reader_modal";
import { ListElementSelector } from "../add_customer_account/ListElementSelector";
import SimpleMenu from "../states_by_date/SimpleMenu";
import ModifyObjectDialog from "../Dialogs/ModifyObjectDialog";


export class ControlComponent extends Component {
    constructor(props) {
        super(props);
        this.state = {
            dataToShow: [],
            accountSelected: null,
            collectionPath: null,
            simpleMenuData: {
                simpleMenuAnchorEl: null,
                elementToModify: null,
            }
        }
    }

    showButtonToChangeDataToShow() {
        let arrayOfData = [
            {name: 'Comptes', collectionPath: ACCOUNTS_PATH},
            {name: 'Tickets', collectionPath: TICKETS_PATH},
            {name: 'Factures', collectionPath: BILLS_PATH},
            {name: 'Avoirs', collectionPath: CREDIT_NOTES_PATH},
            {name: 'FacturesAvoirs', collectionPath: CREDIT_NOTES_BILL_PATH},
        ];
        return <div style={{margin: '10px'}}>
            {arrayOfData.map(x => <button key={x.name}
                                          style={this.state.collectionPath === x.collectionPath ? {backgroundColor:"#636363", color:"#e9e9e9"} : {}}
                                          onClick={() => this.onClickOnDataCollection(x.collectionPath)}>{x.name}</button>)}
        </div>
    }

    onClickOnDataCollection(collectionPath) {
        this.setState({collectionPath: collectionPath});
        this.getDataFromServer(collectionPath);
    }

    getDataFromServer(collectionPath) {
        let requestFilter = {};
        if (this.state.accountSelected) {
            requestFilter = {"$or": [{"customerAccount": this.state.accountSelected._id}, {"customerId": this.state.accountSelected._id}]}
        }
        ServerService.getFromServer(collectionPath, requestFilter).then(response => this.setState({dataToShow: response}))
    }

    onAccountSelected(accountSelected) {
        this.setState({accountSelected: accountSelected});
        this.setModalVisibility(false);
    }

    setModalVisibility(bool) {
        this.setState({modalVisibility: bool});
    }

    onDataUpdate(dataToUpdate, cb) {
        ServerService.updateOnServer(this.state.collectionPath, dataToUpdate)
            .then(() => cb())
            .then(() => this.modifyElementInDataToShow(this.state.collectionPath, dataToUpdate))
    }

    modifyElementInDataToShow(collectionPath, newElement) {
        let newDataToShow = [...this.state.dataToShow];
        let indexNewElement = newDataToShow.findIndex(x => x._id === newElement._id);
        console.log(newDataToShow, newElement, indexNewElement);
        if (indexNewElement > -1) {
            newDataToShow[indexNewElement] = {...newElement};
            this.setState({dataToShow: newDataToShow})
        }
    }

    render() {
        return <div>
            <Button className="validate-ticket-button"
                    onClick={() => this.setModalVisibility(true)}>
                {this.state.accountSelected ? this.state.accountSelected.name : 'Choisir un compte'}
            </Button>
            {this.showButtonToChangeDataToShow()}
            <EasyDataTable
                // allAccount={this.state.allAccount}
                // dataToShow={this.state.allTickets}
                dataToShow={this.state.dataToShow}
                // onDataUpdate={(x, cb) => this.onDataUpdate(x, cb)}
                onElementClick={
                    (event, elementToModify) => {
                        console.log('modification TODO', event);
                        event.preventDefault();
                        this.setState({
                            simpleMenuData: {
                                simpleMenuAnchorEl: event.target,
                                elementToModify: elementToModify
                            }
                        })
                    }
                }
            />

            <SimpleMenu anchorEl={this.state.simpleMenuData.simpleMenuAnchorEl}
                        handleClose={() => {
                            this.setState({
                                simpleMenuData: {
                                    simpleMenuAnchorEl: null,
                                    elementToModify: null
                                }
                            });
                        }}
                        menuItems={[{
                            text: "Modifier element",
                            action: () => this.setState({
                                simpleMenuData: {
                                    ...this.state.simpleMenuData,
                                    simpleMenuAnchorEl: null,
                                },
                                modifyElementModalOpen: true,
                            }),
                        }]}
            />

            <ModifyObjectDialog open={this.state.modifyElementModalOpen}
                                onClose={() => this.setState({modifyElementModalOpen: false})}
                                title="Modifier objet"
                                objectToModify={this.state.simpleMenuData.elementToModify}
                                onValidate={(x, cb) => this.onDataUpdate(x, cb)}
            />
            <AirReaderModal visible={this.state.modalVisibility}
                            onOutsideClick={() => this.onAccountSelected(null)}>
                <ListElementSelector onClick={x => console.log(x)}
                                     onValid={accountSelected => {
                                         this.onAccountSelected(accountSelected);
                                     }}
                                     sortBy='name'
                                     data={this.props.allAccount}
                                     dataKeysToShow={['name', 'customer reference']}
                                     filterPossibilities={['name', 'customer reference']}
                                     multiChoice={false}
                />
            </AirReaderModal>
        </div>
    }
}