export class sortFunctions {
    static sortAlphabatically(a, b) {
        let x = a.toLowerCase();
        let y = b.toLowerCase();
        if (x < y) {
            return -1;
        }
        if (x > y) {
            return 1;
        }
        return 0;
    }

    static sortAlphabaticallyWithKey(key){
        if (Array.isArray(key)) {
            return (a,b) => sortFunctions.sortAlphabatically(
                key.reduce((valueToCompare,currentKey) =>  valueToCompare += (a[currentKey]),''),
                key.reduce((valueToCompare,currentKey) =>  valueToCompare += (b[currentKey]),''))
        }
        return (a,b) => sortFunctions.sortAlphabatically(a[key],b[key]);
    }
}