import React, { Component } from 'react';
import cloneDeep from 'lodash/cloneDeep';
import './addCustomerAccountComponent.css'
import { billServerService } from "../server_classes/serverData";
import { ServerService } from "../server_classes/ServerService";
import { ACCOUNTS_PATH } from "../server_classes/SERVER_CONST";


export class AddCustomerAccountComponent extends Component {
    constructor() {
        super();
        this.state = {
            accountData: {
                'name': '',
                'clientNumber': '',
                'adress': '',
                'city': '',
                'country': '',
                'mail': '',
                'phone': '',
                'customer reference': '',
                'customer account': '',
                'accountlibele': '',
                'entire name': '',
                'with FS': true,
                'paiement type': 'Cash'
            },

            accountDataModel: {
                'name': {inputType: 'text', inputPlaceholder: 'AFV', labelValue: 'Nom abrg'},
                'adress': {inputType: 'text', inputPlaceholder: 'Rue de nachtigal - B.P. 245', labelValue: 'Adresse'},
                'city': {inputType: 'text', inputPlaceholder: 'Yaound', labelValue: 'Ville'},
                'country': {inputType: 'text', inputPlaceholder: 'Cameroun', labelValue: 'Cameroun'},
                'mail': {inputType: 'text', inputPlaceholder: 'afv_cameroun@yahoo.fr', labelValue: 'Mail'},
                'phone': {inputType: 'text', inputPlaceholder: '(+ 237) 6 77 93 78 21 - 22', labelValue: 'Telephone'},
                'customer reference': {
                    inputType: 'text',
                    inputPlaceholder: 'AFV/18/0001465CL',
                    labelValue: 'Reference client'
                },
                'customer account': {inputType: 'text', inputPlaceholder: '411-AFV', labelValue: 'Compte client'},
                'entire name': {
                    inputType: 'text',
                    inputPlaceholder: 'Agence Franaise de Voyages',
                    labelValue: 'Nom entier'
                },
                'with FS': {inputType: 'checkbox', labelValue: 'FS inclus dans facture', inputChecked: true},
                'paiement type': {inputType: 'text', inputPlaceholder: 'CASH', labelValue: 'Type de paiement'},
            },

        }
    }

    emptyAccountData = {
        'name': '',
        'adress': '',
        'city': '',
        'country': '',
        'mail': '',
        'phone': '',
        'customer reference': '',
        'customer account': '',
        'entire name': '',
        'with FS': true,
        'paiement type': 'Cash'
    }


    insertAccount(self) {
        console.log(self.state.accountData);
        ServerService.postToServer(ACCOUNTS_PATH,this.state.accountData)
            .then(function (id_inserted) {
                let accountCreated = cloneDeep(self.state.accountData);
                accountCreated['_id'] = id_inserted;
                self.resetAccountData();
                self.props.onAddNewAccount(accountCreated);
            });
    }

    updateAccount(self) {
        ServerService.updateOnServer(ACCOUNTS_PATH,this.state.accountData)
            .then(function (result) {
                if (!result){
                    return
                }
                let accountUpdated = cloneDeep(self.state.accountData);
                self.resetAccountData();
                self.props.onUpdateAccount(accountUpdated);
            });
    }
    deleteAccount(self) {
        ServerService.deleteOnServer(ACCOUNTS_PATH, this.state.accountData._id)
        // billServerService.deleteAccount(this.state.accountData._id)
            .then(function (result) {
                if (!result){
                    return
                }
                let accountToDelete = cloneDeep(self.state.accountData);
                self.resetAccountData();
                self.props.onDeleteAccount(accountToDelete);
            });
    }

    resetAccountData() {
        this.setState({
            accountData: cloneDeep(this.emptyAccountData)
        })
    }

    componentDidMount() {
        if (this.props.accountToModify) {
            this.setState({
                accountData: cloneDeep(this.props.accountToModify)
            })
        }
    }


    handleInputChangeOnAccountData(event) {
        console.log(event.target, event.target.value, event.target.checked);
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;

        const name = target.name;
        let newAccountToAdd = cloneDeep(this.state.accountData);
        console.log(newAccountToAdd);
        newAccountToAdd[name] = value;
        this.setState({
            accountData: newAccountToAdd
        });
    }

    setAccountData(accountToClone) {
        this.setState({
            accountData: cloneDeep(accountToClone)
        })
    }

    componentWillReceiveProps(nextProps) {
        if (!nextProps.accountToModify) {
            this.resetAccountData()
        } else if (this.props.accountToModify !== nextProps.accountToModify) {
            this.setAccountData(nextProps.accountToModify)
        }
    }

    modificationMessage() {
        let divToReturn = '';
        if ('_id' in this.state.accountData) {
            divToReturn = <div>
                Mise  jour du compte {this.state.accountData.name}
                <div className="flex-container flex-direction-column">
                    <button onClick={() => this.updateAccount(this)}>Mettre  jour compte client</button>
                    <button onClick={() => this.deleteAccount(this)}>Supprimer compte client</button>
                </div>
            </div>
        } else {
           divToReturn = <div>
                <button onClick={() => this.insertAccount(this)}>Creer nouveau compte client</button>
            </div>
        }
        return divToReturn
    }

    render() {
        // this.setAccountData();
        return (
            <div>
                <InputElements2
                    data={this.state.accountData}
                    dataModel={this.state.accountDataModel}
                    handleInputChangeOnAccountData={(event) => this.handleInputChangeOnAccountData(event)}/>

                {this.modificationMessage()}

            </div>
        )
    }
}


function InputElements2(props) {
    let elementsToShow = Object.keys(props.dataModel).map((x, step) => {
        return <div className="flex-container" key={step}>
            <label className="flex-1-1 text-right">{props.dataModel[x].labelValue}</label>
            {props.dataModel[x].inputType === 'checkbox' ?
                <input
                    name={x}
                    placeholder={props.dataModel[x].inputPlaceholder}
                    type={props.dataModel[x].inputType}
                    onChange={props.handleInputChangeOnAccountData}
                    checked={props.data[x]}
                    className="flex-3-1"/> :
                <input
                    name={x}
                    value={props.data[x]}
                    placeholder={props.dataModel[x].inputPlaceholder}
                    type={props.dataModel[x].inputType}
                    onChange={props.handleInputChangeOnAccountData}
                    className="flex-3-1"/>
            }
        </div>
    });

    return (
        <div className="margin-bottom-20px">
            {elementsToShow}
        </div>
    )
}

