import React, { Component } from 'react';
import { TicketDetails } from "../ticket-validation/TicketDetails";
import { CreditNoteToMakeDetails } from "./CreditNoteToMakeDetails";
import { CreditNoteServerService } from "../server_classes/creditNoteServerRequest";

let emptyCreditNote = {
    ticketNumber: '',
    passengerName: '',
    initialTicketAmount: 0,
    amountUsed: 0,
    cancelationFees: 0,
    netToRefund: 0,
    companyName: '',
    agencyFees: 0,
    trip: '',
    emissionDate: '',
    flightDates: [],
    withFS: true,
};

export class CreditNoteMaker extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ticketNumberToFind: '',
            creditNote: Object.assign(emptyCreditNote, {}),
            creditNoteAlreadyExist: false,
            ticketToRefund: ''
        }
    }


    changeTicketNumber(event) {
        let valueToSet = event.target.value;
        this.setState({
            [event.target.name]: valueToSet
        })
    }

    generateCreditNote(ticketFound, self) {
        let creditNote = Object.assign(this.state.creditNote, {});
        creditNote.ticketNumber = ticketFound.ticketNumber;
        creditNote.passengerName = ticketFound.name;
        creditNote.customerId = ticketFound.customerId;
        creditNote.customerAccount = ticketFound.customerAccount;
        creditNote.trip = ticketFound.trip;
        creditNote.flightDates = ticketFound.flightDates;
        creditNote.emissionDate = ticketFound.date;
        creditNote.companyName = ticketFound.companyName;
        this.parseCreditNoteValuesToInt(creditNote);
        console.log('credit note generated', creditNote);
        CreditNoteServerService.insertCreditNote(creditNote).then(function (response) {
            console.log(response);
            self.props.updateTicket(response['ticketsUpdated']);
            self.props.addCreditNote(response['credit_note']);
            self.findTicketToRefund();
        }).catch(function (error) {
            console.log(error)
        })
    }

    deleteExistingCreditNote(self) {
        if ('_id' in this.state.creditNote) {
            CreditNoteServerService.cancelCreditNote(this.state.creditNote).then(response => {
                console.log(response);
                self.props.updateTicket(response['ticketsUpdated']);
                self.props.deleteCreditNote(this.state.creditNote);
                self.setState({
                    creditNote: emptyCreditNote
                });
                self.findTicketToRefund();
            }).catch(function (error) {
                console.log(error)
            })
        }
    }

    parseCreditNoteValuesToInt(creditNote) {
        creditNote.initialTicketAmount = parseInt(creditNote.initialTicketAmount, 10);
        creditNote.amountUsed = parseInt(creditNote.amountUsed, 10);
        creditNote.cancelationFees = parseInt(creditNote.cancelationFees, 10);
        creditNote.agencyFees = parseInt(creditNote.agencyFees, 10);
        creditNote.netToRefund = parseInt(creditNote.netToRefund, 10);
    }

    updateCreditNote(creditNoteToUpdate = this.state.creditNote, recalculateInitialAmount = false) {
        let newCreditNote = Object.assign({}, creditNoteToUpdate);
        if (recalculateInitialAmount) {
            this.recalculateInitialTicketAmount(newCreditNote, this.state.ticketToRefund);
        }
        this.recalculateCreditNoteTotal(newCreditNote);
        this.setState({
            creditNote: newCreditNote,
        })
    }

    handleInputChangeOnCreditNote(event) {
        const target = event.target;
        const name = target.name;
        const value = target.type === 'checkbox' ? target.checked : this.checkIfStartWithZeroAndEliminateIt(target.value);

        let newCreditNote = Object.assign({}, this.state.creditNote);
        newCreditNote[name] = value;

        this.updateCreditNote(newCreditNote, target.name === 'withFS');
    }

    checkIfStartWithZeroAndEliminateIt(value) {
        if (value.length > 1 && value.toString()[0] === '0') {
            return value.toString().substring(1);
        } else if (value.length === 0) {
            return 0
        }
        return value
    }

    recalculateCreditNoteTotal(creditNote) {
        creditNote.netToRefund = creditNote.initialTicketAmount - creditNote.amountUsed - creditNote.cancelationFees - creditNote.agencyFees;
    }

    recalculateInitialTicketAmount(creditNote, ticketToRefund) {
        creditNote.initialTicketAmount = creditNote.withFS ? ticketToRefund.total : ticketToRefund.priceTTC
    }

    findTicketToRefund() {
        let ticketFound = this.props.allTickets.find(x => x._id.toString().indexOf(this.state.ticketNumberToFind) > -1);

        if (!ticketFound) {
            ticketFound = '';
        } else if (ticketFound) {
            if ('credit_note_refund' in ticketFound) { // let creditNoteAlreadyExist = 'credit_note_refund' in this.state.ticketToRefund
                let creditNoteFound = this.props.allCreditNotes.find(x => x._id === ticketFound.credit_note_refund.credit_note_id);
                console.log('credit Note found', creditNoteFound);
                this.setState({
                    creditNoteAlreadyExist: true,
                    creditNote: creditNoteFound,
                });
            } else {
                this.setState({
                    creditNoteAlreadyExist: false,
                    creditNote: Object.assign({}, emptyCreditNote),
                });
            }
        }
        this.setState({
            ticketToRefund: ticketFound
        });
        setTimeout(() => this.updateCreditNote(this.state.creditNote,true), 100);
    }

    render() {
        let ticketFound = this.props.allTickets.find(x => x._id.toString().indexOf(this.state.ticketNumberToFind) > -1);
        let ticketFoundWiew = '';

        if (this.state.ticketToRefund) {
            let credit_note_element = <div>Pas d'avoir existant </div>;
            if (this.state.creditNoteAlreadyExist) {
                credit_note_element = <div>Avoir dj existant  supprimer </div>
            }

            ticketFoundWiew = <div>
                <TicketDetails className="flex-container "
                               data={this.state.ticketToRefund}
                               handleInputChangeOnTicket={(x) => {
                               }}
                >
                </TicketDetails>
                {this.state.creditNoteAlreadyExist ?
                    <button onClick={() => this.deleteExistingCreditNote(this)}
                            disabled={!this.state.creditNoteAlreadyExist}>
                        Supprimer avoir dj existant
                    </button> :
                    <button onClick={() => this.generateCreditNote(ticketFound, this)}
                            disabled={this.state.creditNoteAlreadyExist}>
                        Valider avoir
                    </button>}

                {credit_note_element}
            </div>;
        }


        return <div className="flex-container flex-direction-column">
            <div>
                <label>Numero de ticket </label>
                <input placeholder="Ticket number" type="text" name="ticketNumberToFind"
                       onChange={event => this.changeTicketNumber(event)}/>
                <button onClick={() => this.findTicketToRefund()}>Trouver billet</button>
            </div>

            {ticketFoundWiew}

            <CreditNoteToMakeDetails ticketToRefund={this.state.ticketToRefund}
                                     creditNote={this.state.creditNote}
                // updateCreditNote = {(newCreditNote)=>this.updateCreditNote(newCreditNote)}
                                     handleInputChangeOnCreditNote={(event) => this.handleInputChangeOnCreditNote(event)}/>

        </div>
    }


}
