export function generateLongDate(dateValue = '') {
    let dateTimeD = '';
    dateTimeD = dateValue ? new Date(dateValue) : new Date();
    let months = ['janvier', 'fvrier', 'mars', 'avril', 'mai', 'juin', 'juillet', 'aout', 'septembre', 'octobre', 'novembre', 'dcembre'];
    let dateToShow = dateTimeD.getDate() + ' ' + months[dateTimeD.getMonth()] + ' ' + dateTimeD.getFullYear();
    return dateToShow
}