import React from 'react';

import { NavLink, Route } from 'react-router-dom'

import { TicketValidationComponent } from "./ticketValidationComponent";
import { BillMaker } from "../bill_maker/bill_maker_component";
import { BillViewer } from "../bill_maker/bill_viewer_component";
import { AccountStatement } from "../account_statement/account_statement_component";

export function TicketView(props) {


    return <div>

        <Route path="/tickets/validate" render={() =>
            <TicketValidationComponent
                allAccount={props.allAccount}
                allTickets={props.allTickets}
                updateTicket={(x) => props.updateTicket(x)}
                addTicket={(ticketToAdd) => props.addTicket(ticketToAdd)}/>
        }/>
        <Route path="/tickets/bill" render={() =>
            <BillMaker
                allAccount={props.allAccount}
                allTickets={props.allTickets}
                updateTicket={(x) => props.updateTicket(x)}
            />
        }/>
        <Route path="/tickets/billed" render={() =>
            <BillViewer
                allAccount={props.allAccount}
                allTickets={props.allTickets}
                updateTicket={(ticket) => props.updateTicket(ticket)}
            />
        }/>
        <Route path="/tickets/fees_bill" render={() =>
            <AccountStatement
                allAccount={props.allAccount}
                allTickets={props.allTickets}
                updateTicket={(x) => props.updateTicket(x)}/>
        }/>
    </div>


}