import React, { Component } from 'react';
import { CreditNoteServerService } from "../server_classes/creditNoteServerRequest";
import { CreditNoteBillMaker } from "./CreditNoteBillMaker";
import { CreditNoteMaker } from "./CreditNoteMaker";
import { CreditNoteBillViewer } from "./CreditNoteBillViewer";
import cloneDeep from 'lodash/cloneDeep';
import { Route } from 'react-router-dom'


const CREDIT_NOTE_BILL_MAKER = 0;
const CREDIT_NOTE_MAKER = 1;
const CREDIT_NOTE_BILL_VIEWER = 2;


export class CreditNoteView extends Component {
    constructor(props) {
        super(props);
        this.state = {
            creditNotes: [],
            creditNoteBills: [],
            viewSelected: CREDIT_NOTE_BILL_MAKER,
        }
    }


    componentDidMount() {
        CreditNoteServerService.getAllCreditNotes().then(allCreditNotesObtained => {
            console.log('allCreditNotes', allCreditNotesObtained);
            this.setState({creditNotes: allCreditNotesObtained})
        });

        CreditNoteServerService.getAllCreditNoteBills().then(allCreditNoteBillsObtained => {
            console.log('allCreditNoteBills', allCreditNoteBillsObtained);
            this.setState({creditNoteBills: allCreditNoteBillsObtained})
        });
    }

    addCreditNote(creditNote) {
        this.setState({
            creditNotes: this.state.creditNotes.concat([creditNote])
        })
    }

    addCreditNoteBill(creditNoteBill, creditNotesModified) {
        console.log(creditNoteBill);
        console.log('creditnotes concatened', this.state.creditNotes.concat([creditNotesModified]));
        this.setState({
            creditNoteBills: this.state.creditNoteBills.concat([creditNoteBill])
        });
        this.updateCreditNotes(creditNotesModified)
    }

    cancelCreditNoteBill(creditNoteBill, creditNotesModified) {
        console.log(creditNoteBill);
        // console.log('new list', this.state.creditNoteBills.splice(
        //     this.state.creditNoteBills.findIndex(x => x._id === creditNoteBill._id), 1, creditNoteBill));
        let newCreditNoteBillsCopy = cloneDeep(this.state.creditNoteBills);
        newCreditNoteBillsCopy.splice(newCreditNoteBillsCopy.findIndex(x => x._id === creditNoteBill._id), 1, creditNoteBill);

        this.setState({
            creditNoteBills: newCreditNoteBillsCopy

        });
        this.updateCreditNotes(creditNotesModified);
    }

    updateCreditNotes(creditNotesModified) {
        let creditNoteArray = cloneDeep(this.state.creditNotes);
        console.log('creditNotesModified', creditNotesModified);
        for (let creditNote of creditNotesModified) {
            console.log('creditNotesModified', creditNote);
            creditNoteArray.splice(
                creditNoteArray.findIndex(x => x._id === creditNote._id), 1, creditNote);
        }
        this.setState({creditNotes: creditNoteArray});
    }

    deleteCreditNote(creditNote) {
        let creditNotesCopy = this.state.creditNotes.slice();
        let creditNoteIndexToDelete = creditNotesCopy.findIndex(x => x._id === creditNote._id);
        console.log('credit note to delete _id found', creditNoteIndexToDelete);
        creditNotesCopy.splice(creditNoteIndexToDelete, 1);
        console.log('after deletion', creditNotesCopy);
        this.setState({
            creditNotes: creditNotesCopy
        })
    }

    changeView(value) {
        this.setState({viewSelected: value})
    }


    render() {
        return <div>

            <Route path="/credit_notes/new" render={() =>
                <CreditNoteMaker
                    allAccount={this.props.allAccount}
                    allTickets={this.props.allTickets}
                    allCreditNotes={this.state.creditNotes}
                    updateTicket={(x) => this.props.updateTicket(x)}
                    addCreditNote={creditNote => this.addCreditNote(creditNote)}
                    deleteCreditNote={creditNote => this.deleteCreditNote(creditNote)}
                />
            }/>
            <Route path="/credit_notes/bill" render={() =>
                <CreditNoteBillMaker
                    allAccount={this.props.allAccount}
                    allTickets={this.props.allTickets}
                    allCreditNotes={this.state.creditNotes}
                    addCreditNoteBill={(creditNoteBill, creditNoteModified) => this.addCreditNoteBill(creditNoteBill, creditNoteModified)}/>
            }/>
            <Route path="/credit_notes/billed" render={() =>
                <CreditNoteBillViewer
                    allAccount={this.props.allAccount}
                    allTickets={this.props.allTickets}
                    allCreditNotes={this.state.creditNotes}
                    allCreditNoteBills={this.state.creditNoteBills}
                    cancelCreditNoteBill={(creditNoteBill, creditNoteModified) => this.cancelCreditNoteBill(creditNoteBill, creditNoteModified)}
                />
            }/>
        </div>
    }

}