import React,  {Component} from "react";

export class ElementsSelector extends Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedDatas: [],
            initialDatas: []
        }
    }


    componentWillReceiveProps(nextProps) {
        // You don't have to do this check first, but it can help prevent an unneeded render
        if (nextProps.selectorDatas.selectedDatas) {
            this.setState({
                selectedDatas: nextProps.selectorDatas.selectedDatas,
                initialDatas: nextProps.selectorDatas.selectedDatas,
            });
        } else {
            this.resetSelectedDatas()
        }
    }

    resetSelectedDatas() {
        this.setState({selectedDatas: []})
    }

    addItemToSelection(element) {
        let newSelectedDatas = Array.from(this.state.selectedDatas);
        if (newSelectedDatas.includes(element)) {
            newSelectedDatas.splice(newSelectedDatas.indexOf(element), 1);
        } else {
            newSelectedDatas.push(element);
        }
        this.setState({selectedDatas: newSelectedDatas})
    }

    createItemsFromDatas() {
        return this.props.selectorDatas.datas.sort().map((element, step) => <div
                onClick={() => this.addItemToSelection(element)}
                key={step}>
                <input type="checkbox" checked={this.state.selectedDatas.includes(element)}/>
                {element ? element.toString() : 'no value'}
            </div>
        )
    }

    selectAllElements() {
        this.setState({selectedDatas: this.props.selectorDatas.datas.map(x => x)})
    }

    validateSelection(dataToSend = this.state.selectedDatas) {
        this.props.selectorDatas.validateFilterValues(dataToSend);
        this.resetSelectedDatas();
    }


    render() {
        return this.props.selectorDatas.display === 'none' ?
            null :
            <div style={{
                position: 'absolute',
                backgroundColor: '#bbbbbb',
                left: this.props.selectorDatas.X,
                top: this.props.selectorDatas.Y,
                display: this.props.selectorDatas.display,
                padding: '15px',
                borderRadius: '4px',
            }}>
                <button onClick={() => {
                    this.resetSelectedDatas()
                }}>None
                </button>
                <button onClick={() => {
                    this.selectAllElements()
                }}>All
                </button>
                <button style={{}} onClick={() => {
                    this.validateSelection()
                }}>Validate
                </button>
                <span style={{
                    position: 'absolute',
                    right: '4px',
                    top: '4px',
                }} onClick={() => this.validateSelection(this.state.initialDatas)}> <i className="fas fa-times"/>
                </span>
                <div style={{overflowY: 'auto', maxHeight: '300px'}}>
                    {this.createItemsFromDatas()}
                </div>
            </div>
    }

}