import React, { Component } from 'react';
import { LabelAndInputElement } from "../ticket-validation/LabelAndInputElement";

export class CreditNoteToMakeDetails extends Component {
    //props ticketToRefund
    constructor(props) {
        super(props);
        this.state = {}
    }

    creditNoteValuesToShow = [
        {dataKeyToShow: "withFS", labelValue: "Avec FS ?", inputType: "checkbox"},
        {dataKeyToShow: "initialTicketAmount", labelValue: "Montant initial du ticket", inputType: "number"},
        {dataKeyToShow: "amountUsed", labelValue: "Montant utilis", inputType: "number"},
        {dataKeyToShow: "cancelationFees", labelValue: "Frais d'annulation", inputType: "number"},
        {dataKeyToShow: "agencyFees", labelValue: "Frais de service", inputType: "number"},
        {dataKeyToShow: "netToRefund", labelValue: "Net  rembourser", inputType: "number"},
    ];

    render() {
        let ticketNumberAndName = this.props.ticketToRefund ?
            <div className="flex-1-1">
                <div>
                    <label>Numero de ticket : </label>
                    {this.props.ticketToRefund.ticketNumber}
                </div>
                <div>
                    <label>Nom du voyageur : </label>
                    {this.props.ticketToRefund.name}
                </div>
                <div>
                    <label>Nom du compte : </label>
                    {this.props.ticketToRefund.customerAccount}
                </div>
            </div> :
            <div className="text-center">No ticket Found</div>

        return <div>
            <div className="flex-container flex-wrap text-left ">
                {ticketNumberAndName}

                <div className={'flex-1-1 ' + (this.props.ticketToRefund ? 'babab ' : 'displayNone')}>
                    {this.creditNoteValuesToShow.map(x => {
                        return <LabelAndInputElement
                            inputValue={this.props.creditNote[x.dataKeyToShow]} labelValue={x.labelValue}
                            keyToModify={x.dataKeyToShow} inputType={x.inputType}
                            key={x.dataKeyToShow}
                            onChange={(event) => this.props.handleInputChangeOnCreditNote(event)}
                        />
                    })}
                </div>

            </div>
            <div>
                {this.props.children}
            </div>
        </div>
    }
}