import React from 'react';
import { LabelAndInputElement } from "./LabelAndInputElement";

export function TicketDetails(props) {
    let idGroupToShow = [
        {dataKeyToShow: "ticketNumber", labelValue: "Numero de ticket", inputType: "text"},
        {dataKeyToShow: "name", labelValue: "Identit du passager", inputType: "text"},
        {dataKeyToShow: "accountName", labelValue: "Nom renseign par agent", inputType: "text"},
        {dataKeyToShow: "customerAccount", labelValue: "Nom de compte  attribuer", inputType: "text"},
    ];

    let flightGroupToShow = [
        {dataKeyToShow: "companyName", labelValue: "Compagnie", inputType: "text"},
        {dataKeyToShow: "trip", labelValue: "Voyages", inputType: "text"},
        {dataKeyToShow: "flightDates", labelValue: "Dates de vol", inputType: "text"},
        {dataKeyToShow: "emissionDate", labelValue: "Dates de creation et modif dossier", inputType: "text"},
        {dataKeyToShow: "date", labelValue: "Date d'emission", inputType: "text"},
    ];

    let billingGroupToShow = [
        {dataKeyToShow: "commissionRate", labelValue: "Commission (% du HT)", inputType: "text"},
        {dataKeyToShow: "commissionValue", labelValue: "Valeur de la commission", inputType: "text"},
        {dataKeyToShow: "priceHT", labelValue: "Prix HT", inputType: "number"},
        {dataKeyToShow: "priceTTC", labelValue: "Prix TTC", inputType: "number"},
        {dataKeyToShow: "fees", labelValue: "Frais de service", inputType: "number"},
        {dataKeyToShow: "total", labelValue: "Total  payer", inputType: "number"},
    ];

    let elementsToShow = <div className="flex-container flex-direction-column">
        <div className="flex-container">
            <div className="flex-1-1 margin-5px padding-5px">
                {
                    idGroupToShow.map(x => {
                        return <LabelAndInputElement
                            inputValue={props.data[x.dataKeyToShow]} labelValue={x.labelValue}
                            keyToModify={x.dataKeyToShow} inputType={x.inputType}
                            key={x.dataKeyToShow}
                            onChange={(event) => props.handleInputChangeOnTicket(event)}
                        />
                    })
                }
            </div>
            <div className="flex-1-1 margin-5px padding-5px">
                {
                    flightGroupToShow.map(x => {
                        return <LabelAndInputElement
                            inputValue={props.data[x.dataKeyToShow]} labelValue={x.labelValue}
                            keyToModify={x.dataKeyToShow} inputType={x.inputType}
                            key={x.dataKeyToShow}
                            onChange={(event) => props.handleInputChangeOnTicket(event)}
                        />
                    })
                }
            </div>
            <div className="flex-1-1 margin-5px  padding-5px">
                {
                    billingGroupToShow.map(x => {
                        return <LabelAndInputElement
                            inputValue={props.data[x.dataKeyToShow]} labelValue={x.labelValue}
                            keyToModify={x.dataKeyToShow} inputType={x.inputType}
                            key={x.dataKeyToShow}
                            onChange={(event) => props.handleInputChangeOnTicket(event)}
                        />
                    })
                }
            </div>
        </div>
        <div>
            <LabelAndInputElement
                labelValue="Billet annul ? "
                keyToModify="canceled" inputType="checkbox"
                onChange={(event) => props.handleInputChangeOnTicket(event)}
                inputValue={props.data['canceled']}
                className="block-container"
            />
        </div>
        {
            Object.keys(props.data).includes('billed') ?
                <div>Facture billet : {props.data['billed'].bill_number} / {props.data['billed'].value_billed.toLocaleString()} FCFA</div>
                : ''
        }
        {
            Object.keys(props.data).includes('billed_fees') ?
                <div>Facture FS : {props.data['billed_fees'].bill_number} / {props.data['billed_fees'].value_billed.toLocaleString()} FCFA</div>
                : ''
        }


    </div>;

    let credit_note_view = '';
    if ('credit_note_refund' in props.data) {
        let creditNoteData = props.data.credit_note_refund;
        let credit_note_date = new Date(creditNoteData['creationDate']).toLocaleString();
        credit_note_view =
            <div>Remboursement effectu : {creditNoteData['value_to_refund']} FCFA le {credit_note_date} </div>
    }


    return (
        <div className="flex-container flex-wrap text-left ticket-container flex-direction-column">
            <div className="ticket-container ">
                {elementsToShow}

                <div className="ticket-container-action-buttons-div">
                    <div className="">{props.children}</div>
                </div>
                <div>{credit_note_view}</div>
            </div>
        </div>
    )
}