            'Compagnie': StatesByDate.retriveValueFromKeyAndValueIfNothing(ticket, 'companyName'),
            'Numero billet': StatesByDate.retriveValueFromKeyAndValueIfNothing(ticket, 'ticketNumber'),
            'Parcours': StatesByDate.retriveValueFromKeyAndValueIfNothing(ticket, 'trip'),
            'Classe': '',
            'Facture': ticket['billed'] ? (ticket['billed']['bill_number'] ? ticket['billed']['bill_number'] : ' ') : ' ',
            'BC/Trip': StatesByDate.retriveValueFromKeyAndValueIfNothing(billOnTicket, 'commandNumber'),
            'CC': StatesByDate.retriveValueFromKeyAndValueIfNothing(billOnTicketRemarks, 'CC'),
            'OI':  StatesByDate.retriveValueFromKeyAndValueIfNothing(billOnTicketRemarks, 'IO'),
            'DEPT': '',
            'PERS': StatesByDate.retriveValueFromKeyAndValueIfNothing(billOnTicketRemarks, 'Commande suivie par '.trim()),
            'PNR': '',
            'HT': parseInt(StatesByDate.retriveValueFromKeyAndValueIfNothing(ticket, 'priceHT', 0), 10),
            'Taxes': parseInt(StatesByDate.retriveValueFromKeyAndValueIfNothing(ticket, 'priceTTC', 0), 10) - parseInt(StatesByDate.retriveValueFromKeyAndValueIfNothing(ticket, 'priceHT', 0), 10),
            'Montant/FCFA': parseInt(StatesByDate.retriveValueFromKeyAndValueIfNothing(ticket, 'priceTTC', 0), 10),
