    convertToCSV(objArray) {
        let array = typeof objArray !== 'object' ? JSON.parse(objArray) : objArray;
        let str = '';

        for (let i = 0; i < array.length; i++) {
            let line = '';
            for (let index in array[i]) {
                if (line !== '') line += ';';
                line += array[i][index];
            }
            str += line + '\r\n';
        }
        return str;
    }

    exportCSVFileButtonLink(items, fileTitle) {
        let headers = {};
        if (items.length < 1) {
            return <a>Pas de tickets  ce jour</a>
        }
        Object.keys(items[0]).map(key => { //On s'inspire du premier lement pour crer le tableau csv
            headers[key] = key
        });
        items.unshift(headers);
        // console.log(headers);

        let jsonObject = JSON.stringify(items);

        let csv = this.convertToCSV(jsonObject);
        let blob = new Blob([csv], {type: 'text/csv;charset=utf-8;'});
        let exportedFileName = fileTitle + '.csv' || 'export.csv';
        console.log(csv);function pick(obj, keys) {
            return keys.map(k => k in obj ? {[k]: obj[k]} : {})
                .reduce((res, o) => Object.assign(res, o), {});
        }
        let url = URL.createObjectURL(blob);
        return <a href={url} download={exportedFileName}>Telecharger {exportedFileName}</a>
    }

    getCustomersAccountName() {
        let customersName = {};
        this.props.allAccount.reduce((completed, x) => {
            let keyToAdd = x._id;
            let valueToAdd = x.name;
            customersName[keyToAdd] = valueToAdd;
        }, {});
        return customersName;
    }

    ksort(src) {
        const keys = Object.keys(src);
        keys.sort();
        return keys.reduce((target, key) => {
            target[key] = src[key];
            return target;
        }, {});
    };

    getTicketsByCustomerName(tickets) {
        // tickets are classed by their customer name
        let customersNames = this.getCustomersAccountName();
        let ticketByCustomerAccountName = {};
        tickets.reduce((unused, ticket) => {
            let ticketCustomerName = customersNames[ticket.customerId];
            if (ticketCustomerName in ticketByCustomerAccountName) {
                ticketByCustomerAccountName[ticketCustomerName].push(ticket)
            } else {
                ticketByCustomerAccountName[ticketCustomerName] = [ticket]
            }
        }, {});
        //Calculate totals for each group and add it as a ticket in first line
        Object.keys(ticketByCustomerAccountName).map(x => {
            ticketByCustomerAccountName[x] = this.calculateTotals(ticketByCustomerAccountName[x]);
            ticketByCustomerAccountName[x][0]['name'] = x;
        });

        ticketByCustomerAccountName['zTotal'] =
            [this.calculateTotals(Object.keys(ticketByCustomerAccountName).map(x => ticketByCustomerAccountName[x][0]))[0]];
        ticketByCustomerAccountName['zTotal'][0]['name'] = 'zTotal';

        console.log(ticketByCustomerAccountName);
        console.log("test contenu", ticketByCustomerAccountName);
        // console.log(this.ksort(cloneDeep(ticketByCustomerAccountName)));
        return this.ksort(ticketByCustomerAccountName)
    }

    calculateTotals(ticketsArray) {
        let totalValue = ticketsArray.reduce((finalValues, ticket) => {
            finalValues['priceHT'] += parseInt(ticket['priceHT'], 10);
            finalValues['priceTTC'] += parseInt(ticket['priceTTC'], 10);
            finalValues['fees'] += parseInt(ticket['fees'], 10);
            finalValues['commissionValue'] += parseInt(ticket['commissionValue'], 10);
            return finalValues
        }, {'priceHT': 0, 'priceTTC': 0, 'fees': 0, 'commissionValue': 0});
        ticketsArray.unshift(totalValue);
        return ticketsArray
    }

