import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Divider from '@material-ui/core/Divider';
import DraftsIcon from '@material-ui/icons/Drafts';
import AccountBoxIcon from '@material-ui/icons/AccountBoxRounded';
import DescriptionIcon from '@material-ui/icons/Description';
import NoteIcon from '@material-ui/icons/Note';
import NoteAddIcon from '@material-ui/icons/NoteAdd';
import ReceiptIcon from '@material-ui/icons/Receipt';
import CloudUploadIcon from '@material-ui/icons/CloudUpload';
import GroupAddIcon from '@material-ui/icons/GroupAdd';
import FlightIcon from '@material-ui/icons/Flight';
import DashboardIcon from '@material-ui/icons/Dashboard';
import BankIcon from '@material-ui/icons/MonetizationOn';


import Collapse from '@material-ui/core/es/Collapse/Collapse';
import Redirect from 'react-router-dom/es/Redirect';

const DIVIDER_MENU_TYPE = 'DIVIDER_CONST';
const COLLAPSE_MENU_TYPE = 'COLLAPSE_MENU_CONST';

const styles = theme => ({
    root: {
        maxWidth: 360,
        width: '250px',
        backgroundColor: theme.palette.background.paper,
        borderRight: '1px solid rgba(0, 0, 0, 0.12)',
        height: '100%',
        marginTop: 0,
    },
    nav: {
        width: '250px',
        overflow: 'scroll',
        position: 'absolute',
        marginTop:'64px',
        top: 0, bottom: 0, left: 0, right: 0,

    },
    navContainer: {
        width: '250px',
    },
    nested: {
        paddingLeft: theme.spacing.unit * 4,
    },
});

class AirReaderMenuList extends React.Component {
    handleListItemClick = (event, index, redirectionAdress) => {
        console.log('click on ', index);
        this.setState({
            selectedIndex: index,
            redirectionAddress: redirectionAdress,
            collapsedMenuSelectedIndex: null,
        });
    };
    handleListItemCollapseClick = (event, index, redirectionAdress) => {
        this.setState({
            collapsedMenuSelectedIndex: index,
            redirectionAddress: redirectionAdress,
        });
    };
    tableauNavbar = [
        {title: 'Tickets', redirectionAddress: null, icon: <DescriptionIcon/>},
        {
            type: COLLAPSE_MENU_TYPE, elements: [
            {title: 'Valider', redirectionAddress: '/tickets/validate'},
            {title: 'Facturer', redirectionAddress: '/tickets/bill'},
            {title: 'Factures cres', redirectionAddress: '/tickets/billed'},
            {title: 'Facturer FS', redirectionAddress: '/tickets/fees_bill'},
        ],
        },
        {title: 'Comptes', redirectionAddress: '/accounts', icon: <AccountBoxIcon/>},
        {title: 'Avoirs', redirectionAddress: null, icon: <NoteIcon/>},
        {
            type: COLLAPSE_MENU_TYPE, elements: [
            {title: 'Creer/Supprimer avoirs', redirectionAddress: '/credit_notes/new'},
            {title: 'Facturer avoirs', redirectionAddress: '/credit_notes/bill'},
            {title: 'Factures avoir cres', redirectionAddress: '/credit_notes/billed'},
        ],
        },
        {type: DIVIDER_MENU_TYPE},
        {title: 'Etats', redirectionAddress: '/states', icon: <NoteAddIcon/>},
        {title: 'Importer AIR', redirectionAddress: '/import', icon: <CloudUploadIcon/>},
        {type: DIVIDER_MENU_TYPE},
        {title: 'Controle Donnes', redirectionAddress: '/controlComponent', icon: <ReceiptIcon/>},
        {title: 'States 2.0', redirectionAddress: '/superDataTable', icon: <GroupAddIcon/>},
        {title: 'B.O. generator', redirectionAddress: '/boComponent', icon: <FlightIcon/>},
        {title: 'Nada', redirectionAddress: '/nada', icon: <DraftsIcon/>},
        {title: 'Dashboard', redirectionAddress: '/dashboard', icon: <DashboardIcon/>},
        {title: 'Bank', redirectionAddress: '/bank', icon: <BankIcon/>},
    ];


    state = {
        selectedIndex: 1,
        redirectionAddress: null,
        collapsedMenuSelectedIndex: null,
    };

    generateMenuListItemFromData(dataArray, self = this) {
        return dataArray.map((navBarElement, step) => {
            switch (navBarElement.type) {
                case DIVIDER_MENU_TYPE:
                    return <Divider key={step}/>;
                case COLLAPSE_MENU_TYPE:
                    return <Collapse in={self.state.selectedIndex === step - 1} key={step} timeout="auto" unmountOnExit>
                        <List component="div" disablePadding>
                            {
                                navBarElement.elements.map((navBarCollapseElement, stepCollapseElement) =>
                                    <AirReaderSimpleMenuItem itemToShow={navBarCollapseElement}
                                                             key={stepCollapseElement}
                                                             selectedIndex={self.state.collapsedMenuSelectedIndex}
                                                             itemNumber={stepCollapseElement}
                                                             className={self.props.classes.nested}
                                                             handleListItemClick={self.handleListItemCollapseClick}
                                    />)
                            }
                        </List>
                    </Collapse>;
                default:
                    return <AirReaderSimpleMenuItem key={step} itemToShow={navBarElement}
                                                    selectedIndex={self.state.selectedIndex}
                                                    itemNumber={step}
                                                    handleListItemClick={self.handleListItemClick}
                    />;
            }
        });
    }

    checkRedirection() {
        let redirectionAddress = this.state.redirectionAddress;
        if (redirectionAddress) {
            this.setState({
                redirectionAddress: null,
            });
            return <Redirect to={redirectionAddress}/>;
        }
        return '';
    }

    render() {
        const {classes} = this.props;

        return (
            this.state.redirectionAddress ? this.checkRedirection() :
                <div className={classes.root}>
                    <List component="nav" className={classes.nav}>
                        {this.generateMenuListItemFromData(this.tableauNavbar)}
                        {/*{this.state.menuToShow}*/}
                    </List>
                </div>
        );
    }
}

let AirReaderSimpleMenuItem = (props) => {
    let {itemToShow, itemNumber, selectedIndex, handleListItemClick, className} = props;
    return <ListItem
        button
        className={className}
        selected={selectedIndex === itemNumber}
        onClick={event => {
            handleListItemClick(event, itemNumber, itemToShow.redirectionAddress);
        }}
    >
        {itemToShow.icon ? <ListItemIcon>
            {itemToShow.icon}
        </ListItemIcon> : ''}
        <ListItemText inset primary={itemToShow.title}/>
    </ListItem>;
};

AirReaderMenuList.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(AirReaderMenuList);