import React, { Component } from 'react';
import { AddCustomerAccountComponent } from "./addCustomerAccountComponent";
import { CustomerAccountsSelector } from "./showCustomerAccounts";


export class CustomersComponent extends Component {
    constructor(props) {
        super(props);
        // this.handleClick = this.handleClick.bind(this)
        // this.inputChange = this.inputChange.bind(this)
        this.state = {
            accountToModify: ''
        };
    }

    setAccountToModify(accountToModify) {
        this.setState({
            accountToModify: accountToModify,
        })
    }

    clickOnNewAccountButton() {
        this.setAccountToModify(null)
    }

    render() {
        return <div className='flex-container'>
            <div>

                <CustomerAccountsSelector
                    data={this.props.allAccount}
                    onValid={accountSelected => this.setAccountToModify(accountSelected)}
                    multiChoice={false}/>
            </div>
            <div>
                <AddCustomerAccountComponent
                    onAddNewAccount={x => this.props.addNewAccount(x)}
                    onUpdateAccount={x => this.props.updateAccount(x)}
                    onDeleteAccount={x => this.props.deleteAccount(x)}
                    accountToModify={this.state.accountToModify}/>
                <button onClick={() => this.clickOnNewAccountButton()}> Nouveau compte</button>
            </div>
        </div>
    }
}

