import React from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import {ServerService } from "../server_classes/ServerService";

export default class ConnexionDialog extends React.Component {
    state = {
        username: '',
        password: '',
        connexionRunning: false,
    };


    handleInputChange(event) {
        let target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        this.setState({
            [event.target.name]: value,
        });
    }

    setConnexionRunning(bool) {
        this.setState({connexionRunning: bool})
    }

    connectAndRetrieveToken(self = this) {
        let userCredentials = {
            name: this.state.username,
            password: this.state.password
        };
        this.setConnexionRunning(true);
        ServerService.sendDataToRetrieveToken(userCredentials).then(token => {
            console.log('token retrieved');
            self.setConnexionRunning(false);
            if (!token) return;
            self.setState({password: ''});
        }, error => {
            self.setConnexionRunning(false);
        })
    }

    onEnterClick(event) {
        if (event.key === 'Enter') {
            this.connectAndRetrieveToken()
        }
    }

    render() {
        return (
            <Dialog
                open={this.props.connexionDialogOpen}
                onClose={() => null}
                aria-labelledby="form-dialog-title"
                onKeyPress={(event) => this.onEnterClick(event)}
            >
                <DialogTitle id="form-dialog-title">Connexion</DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Veuillez vous reconnecter svp
                    </DialogContentText>
                    <TextField
                        autoFocus
                        margin="dense"
                        name="username"
                        label="Nom utilisateur"
                        type="text"
                        value={this.state.username}
                        onChange={(event) => this.handleInputChange(event)}
                        fullWidth
                    />
                    <TextField
                        margin="dense"
                        name="password"
                        label="Mot de passe"
                        type="password"
                        value={this.state.password}
                        onChange={(event) => this.handleInputChange(event)}
                        fullWidth
                    />
                </DialogContent>
                <DialogActions>
                    <Button onClick={() => this.props.handleClose(null)} color="primary">
                        Annuler
                    </Button>
                    <Button onClick={() => this.connectAndRetrieveToken()} color="primary"
                            disabled={this.state.connexionRunning}>
                        Valider
                    </Button>
                </DialogActions>
            </Dialog>
        );
    }
}