function TestModal(props) {

    return <Dialog fullScreen
                   open={props.open}
                   onClose={props.onClose} >

        <div>Bananan</div>
    </Dialog>
}


function TicketTripDetails(props) {
    if (props.ticket) {
        let tripInDetail = props.ticket.tripsInDetail;
        let tripInDetailDiv = tripInDetail.map(x => x['name']);


        return <div className="flex-container-column" style={{width: '600px'}} onClick={() => props.onClick()}>
            <div className="flex-container">
                <span className="flex-1-1 flex-centered">{props.ticket['name']}</span>
                <span className="flex-1-1"><div className="bold-text">TKT {props.ticket['ticketNumber']}</div>
                    {props.ticket['trip']}</span>
                <span className="flex-1-1 flex-centered">{props.ticket['accountName']}</span>
            </div>
            <div className="flex-container-column flex-centered">
                {props.ticket['tripsInDetail'].map((trip, step) => <TripPortionLine
                    style={{width: '600px', border: '1px solid grey', borderRadius: '5px'}} trip={trip} key={step}/>)}
            </div>
        </div>
    }
    return <div></div>
}

function handleTime(time) {

    if (time.trim().length === 4) {
        return time[0] + time[1] + ":" + time[2] + time[3]
    }
    return time
}


function handleDateTime(datetime) {
    if (datetime.trim().length !== 9) {
        return datetime
    }
    return datetime[0] + datetime[1] + ' ' + datetime[2] + datetime[3] + datetime[4] + ' | ' + handleTime(datetime.slice(5));
}

function TripPortionLine(props) {
    if (props.trip) {
        let trip = props.trip
        return <div className="" style={props.style}>
            <span className={'padding-5px ' + (trip.tripType === 'O' ? 'displayNone' : '')}>
                Correspondance</span>
            <div className="flex-container">
                <div className="padding-5px flex-1-1">
                    <div>{trip.departureCity + ' (' + trip.departureAirportCode + ')'}</div>
                    <div>
                        <i className="fas fa-plane-departure padding-5px"/>
                        {handleDateTime(trip.departureDayAndTime)}
                    </div>
                </div>
                <div className="flex-centered">

                    <div><i className="far fa-clock padding-5px"></i>{handleTime(trip.tripLength)}</div>
                </div>
                <div className="padding-5px flex-1-1">
                    <div>{trip.arrivalCity + ' (' + trip.arrivalAirportCode + ')'}</div>
                    <div>
                        <i className="fas fa-plane-arrival padding-5px"></i>
                        {handleDateTime(trip.arrivalDayAndTime)}
                    </div>
                </div>
            </div>
            <div className="flex-container">
                <span className="padding-5px flex-1-1"><div>Max weight  </div>
                    {trip.luggageNumber}</span>
                <span className="padding-5px flex-1-1"><div>Flight number </div>
                    {trip.flightNumber}</span>
                <span className="padding-5px flex-1-1"><div>Company </div>
                    {trip.company}</span>

            </div>
        </div>
    }
    return <div>No trip data</div>
}


