import React from 'react';
import { ListElementSelector } from "./ListElementSelector";


function CustomerAccountElement(props) { //dataKeysToShow, selected
    let dataKeysToShow = props.dataKeysToShow;
    let selectedElementStyle = (props.selected ? 'bold-text' : '');

    return <div onClick={props.onClick}
                onContextMenu={e => props.onContextMenu(e)}
                className={selectedElementStyle}>
        <div>{dataKeysToShow.map(x => props.data[x] + "  ")}</div>
    </div>
}

export function ListElement(props) {
    let dataAlreadySelected = props.dataAlreadySelected;
    let dataToRender = props.dataToRender.map((x, step) =>
        <CustomerAccountElement
            key={step}
            data={x}
            onClick={() => props.onElementClick(x)}
            onContextMenu={e => props.onElementContextMenu(e, x)}
            dataKeysToShow={typeof props.dataKeysToShow === 'function' ? props.dataKeysToShow(x) : props.dataKeysToShow}
            // dataKeysToShow={props.dataKeysToShow}
            selected={dataAlreadySelected ? dataAlreadySelected.includes(x) : ''}
        />);
    return <div className={"list-element-div scrollable text-left " + props.className}>{dataToRender}</div>
}

export function TableElement(props) {
    let dataAlreadySelected = props.dataAlreadySelected;
    let dataToRender = props.dataToRender.map((x, step) =>
        <CustomerAccountElement
            key={step}
            data={x}
            onClick={() => props.onElementClick(x)}
            dataKeysToShow={props.dataKeysToShow}
            selected={dataAlreadySelected ? dataAlreadySelected.includes(x) : ''}
        />);
    return <div className={"list-element-div scrollable text-left " + props.className}>{dataToRender}</div>
}

export function CustomerAccountsSelector(props) {
    console.log("propsCustomerAccount", props, props.multiChoice);
    return <ListElementSelector
        onValid={props.onValid}
        data={props.data}
        sortBy='name'
        dataKeysToShow={props.dataKeysToShow ? props.dataKeysToShow : ['name', 'customer account', 'customer reference']}
        filterPossibilities={props.filterPossibilities ? props.filterPossibilities : ['customer reference', 'name', 'entire name']}
        multiChoice={props.multiChoice === true}/>
}
