import React, { Component } from 'react';
import { billServerService } from '../../server_classes/serverData';
import { Utf8 } from '../../useful_functions/Utf8';


export class ImportBankData extends Component {
    constructor(props) {
        super(props);
        this.state = {
            filesToSendAttributes: [],
            filesToSendRead: [],
            fileSentResult: '',
            bankDataRead: [],
            bankDataTableRowsToImport: [],
        };
    }

    componentDidMount() {
        // billServerService.getAllAirFiles();

    }


    handleFileToSendChange(event) {
        const target = event.target;
        this.setState({
            filesToSendAttributes: Array.from(target.files),
            filesToSendRead: [],
        });
        setTimeout(() => this.readAllFiles(), 100);
        console.log(Array.from(target.files));
    }

    addToFileToSend(newFileToAdd) {
        this.setState({
            filesToSendRead: this.state.filesToSendRead.concat([newFileToAdd]),
        });
    }

    readFileAndAddItToFilesToSend(file, self = this) {
        let fileReader = new FileReader();
        fileReader.onloadend = () => {
            self.addToFileToSend({name: file.name, type: 'AIR', data: fileReader.result});
            self.handleEcobankDatas(fileReader.result);
        };
        fileReader.readAsBinaryString(file);
    }

    readAllFiles() {
        for (let fileToRead of this.state.filesToSendAttributes) {
            this.readFileAndAddItToFilesToSend(fileToRead, this);
        }
    }

    sendAirFiles(self = this) {
        billServerService.sendAIRFiles(this.state.filesToSendRead).then(response => self.showImportedFiles(response));
    }

    showImportedFiles(importedFiles) {
        let importedFilesDiv = importedFiles.map(x => <div
            key={x['ticketNumber']}>{x['ticketNumber'] + ' / ' + x['name']}</div>);
        importedFilesDiv.unshift(<div
            key="summary">{importedFiles.length + ' tickets imports. Veuillez actualiser la page'}</div>);
        this.setState({
            fileSentResult: importedFilesDiv,
        });
    }

    testAllFilesWereRead() {
        if (!this.state.filesToSendAttributes.length) return 'Pas de fichier slectionn';
        let filesReadNames = this.state.filesToSendRead.map(x => x.name);
        for (let fileInitialName of this.state.filesToSendAttributes.map(x => x.name)) {
            if (!filesReadNames.includes(fileInitialName)) {
                return 'En chargement';
            }
        }
        return <button
            onClick={() => this.handleSCBDatas(this.state.filesToSendRead[0].data)}>Aucune action pour le moment</button>;
    }

    handleSCBDatas(text) {
        console.log(text);
        let dataLines = text.split(/\n|\u21b5/);
        let bankDataTableRows = [];
        let dataTransactions = dataLines.map(line => {
            let lineSplited = line.split(';');
            bankDataTableRows.push(
                <tr>
                    {lineSplited.map((x, step) =>
                        <td style={{border: '1px solid black'}} key={step}>{Utf8.decode(x)}</td>)
                    }
                </tr>,
            );
            return {
                operationDate: lineSplited[0],
                description: lineSplited[1],
                debit: lineSplited[2],
                credit: lineSplited[3],
            };
        });
        this.setState({
            bankDataRead: dataTransactions,
            bankDataTableRowsToImport: bankDataTableRows,
        });
        console.log(dataTransactions);
    }


    parseFloatEcobank(valueToParse) {
        try {
            return parseFloat(JSON.parse(valueToParse).split(',').join('')).toLocaleString()
        }
        catch (e) {
            return valueToParse
        }
    }

    handleEcobankDatas(text) {
        let dataLines = text.split(/\n{1,2}/);
        let bankDataTableRows = [];
        let dataTransactions = dataLines.splice(10).map((line,step) => {
            let lineSplited = line.split(/,(?=(?:(?:[^"]*"){2})*[^"]*$)/).splice(2);
            bankDataTableRows.push(
                <tr key={step}>
                    {lineSplited.map((x, step) =>
                        <td style={{border: '1px solid black'}} key={step}>{this.parseFloatEcobank(x)}</td>)
                    }
                </tr>,
            );
            console.log(lineSplited,  this.parseFloatEcobank(lineSplited[4]));

            return {
                operationDate: lineSplited[0],
                valueDate: lineSplited[1],
                reference: lineSplited[2],
                description: lineSplited[3],
                debit: this.parseFloatEcobank(lineSplited[4]),
                credit: this.parseFloatEcobank(lineSplited[5]),
                accountValue: this.parseFloatEcobank(lineSplited[6]),
            };
        });
        this.setState({
            bankDataRead: dataTransactions,
            bankDataTableRowsToImport: bankDataTableRows,
        });
        console.log(dataTransactions);
    }

    render() {
        return <div>
            <div>Importation des donnees bank</div>
            <br/>
            <div>Veuillez slectionner le fichier  importer</div>
            <br/>
            <div>
                <input name="filesToUpload[]" id="filesToUpload" onChange={(e) => this.handleFileToSendChange(e)}
                       type="file" multiple/>
            </div>
            <div>{this.testAllFilesWereRead()}</div>
            <br/>
            <div>{this.state.fileSentResult}</div>
            <table>
                <tbody>
                {this.state.bankDataTableRowsToImport}
                </tbody>
            </table>
        </div>;
    }

}


