import React, { Component } from 'react';

export class KeysToShowManager extends Component {
    //props : initialDatas - list and validateNewConfiguration - function
    constructor(props) {
        super(props);
        this.state = {
            selectedDatas: '',
            LOCAL_STORAGE_KEY: 'STATE_KEYS_STORAGE',
            savedConfigurationListInLocalStorage: {},
            configurationName: '',
        }
    }

    componentDidMount() {
        this.retrieveSavedDataListFromLocalStorage()
    }

    componentWillReceiveProps(nextProps) {
        // You don't have to do this check first, but it can help prevent an unneeded render
        if (nextProps.initialDatas !== this.props.initialDatas) {
            this.resetDatas(nextProps.initialDatas);
        }
    }

    retrieveSavedDataListFromLocalStorage() {
        let localStorageArrayRetrieved = localStorage.getItem(this.state.LOCAL_STORAGE_KEY);
        this.setState({
            savedConfigurationListInLocalStorage: localStorageArrayRetrieved ? JSON.parse(localStorageArrayRetrieved) : {}
        })
    }

    addConfigurationToLocalStorage() {
        let newConfigurationList = Object.assign({}, this.state.savedConfigurationListInLocalStorage);
        newConfigurationList[this.state.configurationName] = this.state.selectedDatas;
        localStorage.setItem(this.state.LOCAL_STORAGE_KEY, JSON.stringify(newConfigurationList));
        this.retrieveSavedDataListFromLocalStorage()
    }

    updateTextAreaConfigurationValue(newValue) {
        this.setState({
            selectedDatas: newValue
        });
    }

    updateConfigurationNameValue(newValue) {
        this.setState({
            configurationName: newValue
        });
    }

    resetDatas(newDatas = this.props.initialDatas) {
        this.setState({
            selectedDatas: JSON.stringify(newDatas).replace(/,/g, ',\n'),
        })
    }

    activateConfigurationFromLocalStorage(configuration) {
        this.updateTextAreaConfigurationValue(configuration);
        this.props.validateNewConfiguration(configuration);
    }


    render() {
        console.log(this.state.savedConfigurationListInLocalStorage);
        if (!this.state.selectedDatas) {
            this.resetDatas()
        }
        return <div style={{display: 'flex', justifyContent: 'center', marginBottom: '10px'}}>
            <div>
                {Object.keys(this.state.savedConfigurationListInLocalStorage).map(
                    configurationName => <div
                        key={configurationName}
                        onClick={() => this.activateConfigurationFromLocalStorage(this.state.savedConfigurationListInLocalStorage[configurationName])}>
                        {configurationName}
                    </div>
                )}
            </div>
            <div>
                <div>
                    <button>Columns</button>
                    <button>Filter</button>
                    <button>Subtotal</button>
                </div>
                <textarea
                    value={this.state.selectedDatas}
                    onChange={e => this.updateTextAreaConfigurationValue(e.target.value)}
                    style={{width: '300px', height: '200px', margin: '0px 10px'}}
                />
            </div>
            <div style={{display: 'flex', flexDirection: 'column'}}>
                <button onClick={() => this.props.validateNewConfiguration(this.state.selectedDatas)}>Validate</button>
                <button onClick={() => this.resetDatas()}>Reset</button>
                <br/><br/>
                <input value={this.state.configurationName}
                       style={{textAlign: 'center'}}
                       placeholder="Configuration name"
                       onChange={(e) => this.updateConfigurationNameValue(e.target.value)}/>
                <button onClick={() => this.addConfigurationToLocalStorage()}>Save configuration</button>


            </div>
        </div>
    }
}

