import React, { Component } from 'react';
import './ticketValidationComponent.css';
import cloneDeep from 'lodash/cloneDeep';
import { CustomerAccountsSelector } from '../add_customer_account/showCustomerAccounts';
import { billServerService } from '../server_classes/serverData';
import { TicketDetails } from './TicketDetails';
import { AirReaderModal } from '../air_reader_modal/air_reader_modal';


export class TicketValidationComponent extends Component {
    Ajouter;

    constructor(props) {
        super(props);
        // this.handleClick = this.handleClick.bind(this)
        // this.inputChange = this.inputChange.bind(this)
        this.state = {
            inputValue: 'lol',
            ticketList: [],
            allAccount: this.props.allAccount,
            allTickets: this.props.allTickets,
            modalVisibility: false,
            modalComponent: '',
            dateFilterInputValue: '',
            researchFilterInputValue: '',
            ticketToShowFilterFunction: this.getFilterFunction,
            maxNumberOfTicketsToShow: 10,
        };
    }

    static recalculateTotal(ticketChosen) {
        ticketChosen.total = parseInt(ticketChosen.priceTTC, 10) + parseInt(ticketChosen.fees, 10);
    }

    componentDidMount() {
        //this.getAllTickets(this);
    }

    handleInputChangeOnTicket(event, ticketChosen) {
        const target = event.target;
        const name = target.name;
        const value = target.type === 'checkbox' ? target.checked : target.value;

        let newTicket = cloneDeep(ticketChosen);
        newTicket[name] = value;
        TicketValidationComponent.recalculateTotal(newTicket);
        this.updateTicket(newTicket);
    }

    addCustomerAccount(ticket, customerAccount) {
        let ticketToAddAccount = cloneDeep(ticket);


        if (customerAccount) {
            ticketToAddAccount['valid'] = true;
            ticketToAddAccount['customerId'] = customerAccount['_id'];
            ticketToAddAccount['customerAccount'] = customerAccount['name'];
        }
        this.updateTicket(ticketToAddAccount);
    }

    updateTicket(ticket) {
        this.props.updateTicket(ticket);
    }

    openModalAndChooseAccountForTicket(self) {
        return new Promise(function (resolve, reject) {
            let customerUniqueAccountSelector = <CustomerAccountsSelector
                onValid={accountChosen => {
                    self.setModalVisibility(false);
                    resolve(accountChosen);
                }}
                data={self.props.allAccount}
            />;
            self.setModalVisibility(true, customerUniqueAccountSelector);
        });
    }

    validateTicket(ticket, self) {
        let validatedTicket = cloneDeep(ticket);
        validatedTicket['checked'] = true;
        billServerService.validateTicket(validatedTicket)
                         .then(function (response) {
                             if (response) {
                                 self.updateTicket(validatedTicket);
                             }
                             else {
                                 console.log('erreur lors de l\'enregistrement...');
                                 //IL faut que je marque un message en cas d'erreur ....
                             }
                         });
    }

    setModalVisibility(visibility, compToShow) {
        if (this.state.modalVisibility !== visibility || visibility) {
            this.setState({
                modalVisibility: visibility,
            });
        }
        if (compToShow) {
            this.setState({
                modalComponent: compToShow,
            });
        }
    }

    changeTicketView(event) {
        let newFilterFunction;
        if (event.target.name === 'valid') {
            newFilterFunction = this.getFilterFunction(true);
        } else {
            newFilterFunction = this.getFilterFunction(false);
        }
        this.setState({
            ticketToShowFilterFunction: newFilterFunction,
        });
    }

    getFilterFunction(bool) {
        let firstFunction = '';
        if (bool) {
            firstFunction = x => x['checked'] === true;

        } else {
            firstFunction = x => x['checked'] !== true;
        }
        return x => firstFunction(x)
            && x['date'].indexOf(this.state.dateFilterInputValue) > -1
            && (x['ticketNumber'].indexOf(this.state.researchFilterInputValue) > -1
                || x['name'].indexOf(this.state.researchFilterInputValue) > -1
                || x['accountName'].indexOf(this.state.researchFilterInputValue) > -1);
    }

    handleFilterInputChange(event) {
        let valueToSet = event.target.value;
        if (event.target.name === 'dateFilterInputValue') {
            let dateObtained = event.target.value.split('-');
            dateObtained[0] = dateObtained[0].substring(2);
            valueToSet = dateObtained.join('');
        }
        this.setState({
            [event.target.name]: valueToSet.toUpperCase(),
        });

    }

    addEMDSAsNewTicket(ticket, self) {
        return new Promise(function (resolve, reject) {
            let ticketsEMDSDetailsToCreate = <EMDSAsNewTicketComponent
                ticketData={ticket}
                onValid={ticketCreated => {
                    self.setModalVisibility(false);
                    console.log('ticketCreated', ticketCreated);
                    resolve(ticketCreated);
                }}
            />;
            self.setModalVisibility(true, ticketsEMDSDetailsToCreate);
        });
    }


    render() {
        let ticketsFiltered = this.props.allTickets
                                  .filter(this.state.ticketToShowFilterFunction);

        let ticketsToShow = ticketsFiltered.slice(0, this.state.maxNumberOfTicketsToShow).map(x => {
            return <TicketDetails className='flex-container ' data={x}
                                  key={x['_id']}
                                  handleInputChangeOnTicket={(event) => this.handleInputChangeOnTicket(event, x)}
            >
                <button className='validate-ticket-button'
                        onClick={() => {
                            this.openModalAndChooseAccountForTicket(this)
                                .then(accountChosen => this.addCustomerAccount(x, accountChosen));
                        }}
                >
                    Affecter compte client
                </button>
                <button className=' validate-ticket-button' disabled={!x.valid}
                        onClick={() => {
                            this.validateTicket(x, this);
                        }}
                >
                    Valider
                </button>
                <button className=' validate-ticket-button' onClick={() => {
                    this.addEMDSAsNewTicket(x, this).then(ticketToAdd => {
                        if (ticketToAdd) {
                            console.log(' ticketTOAdd here', ticketToAdd);
                            this.props.addTicket(ticketToAdd);
                        }
                    });
                }}
                >
                    EMDS
                </button>

            </TicketDetails>;
        });

        return (
            <div className='flex-container-column'>
                <div style={{margin: '10px 2px'}} className='sticky-top-0'>
                    <button onClick={event => this.changeTicketView(event)} name='not_valid'>
                        Billets  valider
                    </button>
                    <button onClick={event => this.changeTicketView(event)} name='valid'>
                        Billets valids
                    </button>

                    <div>Filtrer selon :
                        <input placeholder='Jour' type='date' name='dateFilterInputValue'
                               onChange={event => this.handleFilterInputChange(event)}
                        />
                        <input placeholder='Filtrer' type='text' name='researchFilterInputValue'
                               onChange={event => this.handleFilterInputChange(event)}
                        />
                    </div>
                </div>
                {/*<button onClick={() => this.setModalVisibility(true, addCustomerAccount)}>Afficher Modal</button>*/}
                <AirReaderModal visible={this.state.modalVisibility}
                                onOutsideClick={() => this.setModalVisibility(false, '')}
                >
                    {this.state.modalComponent}
                </AirReaderModal>


                <div className=' all-tickets-scrollable flex-1-1'>
                    {ticketsToShow}
                </div>

            </div>
        );
    }
}


export class EMDSAsNewTicketComponent extends Component {
    constructor(props) {
        super(props);
        // this.handleClick = this.handleClick.bind(this)
        // this.inputChange = this.inputChange.bind(this)
        this.state = {
            ticket: {},
        };
    }

    componentWillReceiveProps(nextProps) {
        // You don' t have to do this check first, but it can help prevent an unneeded render
        console.log('next pros', nextProps);
        if (nextProps.ticketData !== this.props.ticketData) {
            console.log(' on est dedans', nextProps);
            let ticketCopied = this.retrieveTicketFromProps(nextProps);
            this.setState({
                ticket: ticketCopied,
            });
        }
    }

    componentDidMount() {
        if (this.props.ticketData) {
            this.setState({
                ticket: this.retrieveTicketFromProps(this.props),
            });
        }
    }

    retrieveTicketFromProps(props) {
        if (props.ticketData) {
            console.log(' on est dedans', props);
            let ticketCopied = cloneDeep(props.ticketData);
            ticketCopied.referenceTicket = ticketCopied._id;
            ticketCopied.isEMDS = true;
            ticketCopied[' ticketNumber'] = '';
            ticketCopied.commissionRate = 0;
            ticketCopied.commissionValue = 0;
            ticketCopied.fees = 0;
            ticketCopied.total = 0;
            ticketCopied.priceHT = 0;
            ticketCopied.priceTTC = 0;
            delete ticketCopied._id;
            delete ticketCopied[' credit_note_refund'];
            delete ticketCopied.billed;
            console.log(' ticket to create', ticketCopied);

            return ticketCopied;
        }
        return {};
    }

    handleInputChangeOnEMDSTicketInCreation(event) {
        const target = event.target;
        const name = target.name;
        const value = target.type === ' checkbox' ? target.checked : target.value;

        let newTicket = cloneDeep(this.state.ticket);
        newTicket[name] = value;

        TicketValidationComponent.recalculateTotal(newTicket);
        this.setState({
            ticket: newTicket,
        });
        console.log(value, name, newTicket);
    }

    createTicket() {
        let ticketCreated = this.state.ticket;
        ticketCreated._id = ticketCreated[' ticketNumber'];
        console.log(ticketCreated);
        billServerService.insertTicket(ticketCreated).then(() => {
            this.props.onValid(ticketCreated);
        });
    }


    render() {
        return <div>
            <TicketDetails
                className=' flex-container '
                data={this.state.ticket}
                handleInputChangeOnTicket={(event) => this.handleInputChangeOnEMDSTicketInCreation(event)}
            >
                <button onClick={() => this.createTicket()} disabled={!this.state.ticket.ticketNumber}>Create EMDS
                </button>

            </TicketDetails>
        </div>;
    }
}


;